import {apishka} from '../Api';
import * as u_methods from './UserMethods'
import {NotificationManager} from 'react-notifications';	


	export function sendSocket(socket) {
		let idcol = (this.state.config.filter((x) => x.col.toUpperCase() === 'ID' && !x.related)[0] || {}).title
		let ids = []
		this.state.data.forEach((x) =>  ids.push(x[idcol]))
		
		socket.send(JSON.stringify({"viewpath":this.state.path, "ids":ids}))
	}
			
	export function queryBuilder(itm, item) {
		let squery = '?'
		if (itm.paramtype && itm.paramtype === "link") {
			squery = '/'
		}
		if (itm.parametrs) {
			if (itm.paramtype && itm.paramtype === "link") {
				itm.parametrs.forEach((obj) => {				
					if (obj.paramcolumn) {
						squery += item[(this.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0] || {}).title] + "/"
					} else {
						squery += obj.paramconst + "/"
					}
				}) 
			} else {
				itm.parametrs.forEach((obj) => {
					if (!obj.paramt || obj.paramt === 'simple')		
						if (obj.paramcolumn) {
							squery += obj.paramtitle + "=" + item[(this.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0] || {}).title] + "&"
						} else {
							squery += obj.paramtitle + "=" + obj.paramconst + "&"
						}
				}) 
			}

		}
		return squery
	}
	
