	export function  queryBuilder2(itm) {
		let squery = ''
		if (itm.act.indexOf('?') === -1)
			squery = '?'
		if (itm.parametrs) {
			itm.parametrs.forEach((obj) => {
				if (obj.paramcolumn) {
					squery += obj.paramtitle + "=" + 
						(this.state.data[(this.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0] || {}).title] 
							|| 
						 this.state.inputs[(this.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0] || {}).title]) + '&'
				}
				else {
					squery += obj.paramtitle + "=" + obj.paramconst + '&'
				}
			}) 
		}
		
		squery = squery.substring(0,squery.length-1)
		return squery
	}	
	
	
	export function printQueryBuilder() {
		let s = this.state.query || ''
		if (s.length === 0) s = '?'
		else s += '&'
		if (this.state.paramfilters) 
			Object.keys(this.state.paramfilters).forEach((x) => s += x + '=' + this.state.paramfilters[x] + '&')
		return s		
	}
	

	
	export function ifParse(val) {
		if (val) {
			return val
		} else {
			return []
		}
	} 
	
	export function format(val) {
		if (val) {
			val = val.toString()
		}
		return val
	}
	
	export function visibleCondition(data,visible_condition, inputs) {
		if (!inputs) {
			inputs = {}
		}
		if (!visible_condition || visible_condition.length === 0 ) {
			return true;
		} else {
			let a = true

			visible_condition.forEach((item) => {
				if (item.operation.js === ">") {
					if ((data[item.col.value] && data[item.col.value].toString()>item.value) || 
						
						(inputs[item.col.value] && inputs[item.col.value].toString()>item.value)) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "===") {
					if ( (data[item.col.value] && data[item.col.value].toString()===item.value) || 
					(inputs[item.col.value] && inputs[item.col.value].toString()===item.value)
					) {						
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "<") {
					if ((data[item.col.value] && data[item.col.value].toString()<item.value) || 
					(inputs[item.col.value] && inputs[item.col.value].toString()<item.value)
					) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "!==") {
					if ((data[item.col.value] && data[item.col.value].toString() !== item.value) ||
					(inputs[item.col.value] && inputs[item.col.value].toString()!==item.value)
					) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === ">=") {
					if ((data[item.col.value] && data[item.col.value].toString() >= item.value) || 
					(inputs[item.col.value] && inputs[item.col.value].toString()>=item.value)
					) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "<=") {
					if ((data[item.col.value] && data[item.col.value].toString() <= item.value) || 
					(inputs[item.col.value] && inputs[item.col.value].toString()<=item.value)
					) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "===null" ) {					
					if (!(data[item.col.value] !== null || data[item.col.value] !== undefined ) 
					) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "!==null" ) {
					if (!(data[item.col.value] === null || data[item.col.value] === undefined )) {
						
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "indexOf") {
					if (data[item.col.value].indexOf(item.value)!==-1 || (inputs[item.col.value].indexOf(item.value)!==-1)) {
						a = true && a	
					} else {
						a = false
					}	
				}
			})
			return a
		}
	}
	
	export function callApi(uri, itm, item) {
		this.MDLP_API = u_methods.MDLP_API		
	
		function call(self){
			function bodyBuilder(itm, item) {
				let body = {}
				if (itm.parametrs) {
					itm.parametrs.forEach((obj) => {
						if (!obj.paramt || obj.paramt === 'simple')		
							if (obj.paramcolumn) {
								body[obj.paramtitle] = (item[(self.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0] || {}).title] || 
									self.state.data[0][(self.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0] || {}).title])
							} else {
								
								if (obj.paramconst === "_checked_")
									obj.paramconst = self.state.checked
								body[obj.paramtitle] = obj.paramconst 
							}
					}) 
				}
				return body
			}
			let body = {}
			if (itm.actapitype === "GET")
				uri = uri +  self.queryBuilder(itm,item)
			else
				body = bodyBuilder(itm,item)
			self.setState({loading:true},() => {
				apishka(itm.actapitype, body, {}, uri, 
					(data) => {
						self.setState({loading:false}, () => {
							if (itm.actapirefresh) {
								self.getData()
							}
							NotificationManager.success("OK")
						})
					}, (err) => {
						self.setState({loading:false}, () => {
							if (err.response && err.response.data) {
								NotificationManager.error(err.response.data.message)
							}
							else {
								NotificationManager.error('unknown error!')
							}	
						})
					}
				)
			})	
		}
		if (!itm.actapimethod || itm.actapimethod === 'simple')
			if (itm.actapiconfirm) {
				if (window.confirm('Confirm!')){ 
					call(this)
				}
			} 
			else
				call(this)
			
		else if (!itm.actapimethod || itm.actapimethod === 'mdlp')	
			this.MDLP_API(uri, itm, item)
	}