import React, { Component } from 'react';
import Modal from 'react-responsive-modal';
import Select from 'react-select';


const actions = [
	{label:'=',value:'='},
	{label:'>',value:'>'},
	{label:'<',value:'<'},
	{label:'>=',value:'>='},
	{label:'<=',value:'<='},
	{label:'like',value:'like'},
	{label:'not like',value:'not like'},
	{label:'in',value:'in'},
	{label:'not in',value:'not in'},
	{label:'is null',value:'is null'},
	{label:'is not null',value:'is not null'}
]

const bool = [
	{value:'and',label:'and'},
	{value:'or',label:'or'}
]

export default class DefaultModal extends Component {

	render() {
		return (
				<Modal 
					open={this.props.cntxt.state.defmodal} 
					onClose={this.props.cntxt.closeDefModal} center>
				  <h2>{this.props.cntxt.state.defcol}</h2>
				  
				  <div>
					<label>bool:</label> <Select onChange = {(e)=>this.props.cntxt.setState({defbool:e})} options = {bool}/>
					<label>act:</label> <Select onChange = {(e)=>this.props.cntxt.setState({defact:e})} options = {actions}/>
					<label>val:</label> <input value = {this.props.cntxt.defval} onChange = {(e) => this.props.cntxt.setState({defval:e.target.value})} /> 
					<button className = 'btn btn-success' onClick = {()=>{
						let defvalue = this.props.cntxt.state.defvalue
						
						if (defvalue.filter((x) => 
										x.bool.label === this.props.cntxt.state.defbool.label && 
										x.act.label === this.props.cntxt.state.defact.label &&
										x.value === this.props.cntxt.state.defval
									).length === 0 && this.props.cntxt.state.defbool && 
									this.props.cntxt.state.defact && this.props.cntxt.state.defval) {
							defvalue.push({act:this.props.cntxt.state.defact, 
										   value:this.props.cntxt.state.defval, 
										   bool:this.props.cntxt.state.defbool
										   })
							this.props.cntxt.setState({defvalue:defvalue}, ()=> {
									this.props.cntxt.onSettingsChange(
											this.props.cntxt.state.def_title,
											this.props.cntxt.state.def_col,
											"defaultval",
											defvalue,
											this.props.cntxt.state.def_table
										)
								}
							)
						}
					}}>add</button>
					{this.props.cntxt.state.defvalue.map((item) =>
							<div key = {JSON.stringify(item)}> 
								<b>{item.bool.label} </b>
								<b>{item.act.label} </b>
								<label>{item.value}
								</label> <button className ='btn' onClick = {() => {
									let defvalue = this.props.cntxt.state.defvalue
									defvalue = defvalue.filter((x) => 
										!(x.bool.label === item.bool.label && 
										x.act.label === item.act.label &&
										x.value === item.value)
									)
									this.props.cntxt.setState({defvalue:defvalue},()=>this.props.cntxt.onSettingsChange(
												this.props.cntxt.state.def_title,
												this.props.cntxt.state.def_col,
												"defaultval",
												defvalue,
												this.props.cntxt.state.def_table
											))
								}}>-</button>
							</div>
						)
						
					}
				  </div>
				</Modal>
		);
	}
}
