import React, { Component } from 'react';
import Modal from 'react-responsive-modal';
import Select from 'react-select';

export default class ConditionModal extends Component {

	render() {
		return (
			<Modal 
				open={this.props.cntxt.state.modal2} 
				onClose={this.props.cntxt.closeModal2} center>
				<h2>{this.props.cntxt.state.condtitle}</h2>
				  
				<div>
					<b>column:</b>
					{(this.props.cntxt.state.condtitle === "VISIBLE CONDITION")? 
						<Select 
						
							options = {this.props.cntxt.state.paramcolumns}
							value = {this.props.cntxt.state.cond_column}
							onChange = {(selectedOption) => this.props.cntxt.setState({cond_column:selectedOption})}
							onFocus = {() => this.props.cntxt.getParamsColumns()} />	
						:
						<Select 
						
							options = {this.props.cntxt.state.multiselect}
							value = {this.props.cntxt.state.cond_column}
							onChange = {(selectedOption) => this.props.cntxt.setState({cond_column:selectedOption})}
							onFocus = {() => this.props.cntxt.getTableColums(this.props.cntxt.state.cond_relation)} />	
					}
					<b>operation:</b>
					<Select
						options = {[
									{value:"=", label:"=", "js":"===", "python":"=="}, 
									{value:"<>", label:"!=","js":"!==", "python":"!="},
									{"value":">","label":">","js":">", "python":">"},
									{"value":"<","label":"<","js":"<", "python":"<"},
									{"value":">=","label":"<=", "js":"<=", "python":"<="},
									{"value":"like","label":"like", "js":"indexOf", "python":"find"},
									{"value":"in","label":"in", "js":"", "python":"in"},
									{"value":"not in","label":"not in", "js":"", "python":"in"},
									{"value":"is null","label":"is null", "js":"===null", "python":"is None"},
									{"value":"is not null","label":"is not null", "js":"!==null", "python":"is not None"}
						]}
						value = {this.props.cntxt.state.cond_oper}
						onChange = {(selectedOption) => this.props.cntxt.setState({cond_oper:selectedOption})}
						/>
					<b>value</b>
					{(this.props.cntxt.state.condtitle === "VISIBLE CONDITION")? 
						<input className = "form-control" placeholder = "value" value = {this.props.cntxt.cond_value} onChange = {(e)=>this.props.cntxt.setState({cond_value:e.target.value})} />

						:
						<div><Select 
						
							options = {this.props.cntxt.state.paramcolumns}
							value = {this.props.cntxt.state.cond_value}
							onChange = {(selectedOption) => this.props.cntxt.setState({cond_value:selectedOption})}
							onFocus = {() => this.props.cntxt.getParamsColumns()} />	
							<input value = {this.props.cntxt.state.cond_const} onChange = {(e) => this.props.cntxt.setState({cond_const:e.target.value})} placeholder = "const"/>
						</div>
					}
					<button className = "btn btn-outline-primary" onClick = {this.props.cntxt.setCondValue}>add</button>
				</div>
				<div >
					<h3>Conditions:</h3>
					<ul className = "list-group" >
					{this.props.cntxt.state.condvalue.map((item)=>(
							<li className = "list-group-item list-group-item-primary row" key = {item.col.value + item.value}>
							[{item.col.value}]
							<b>{" " + item.operation.value + " "}</b> 
							{(this.props.cntxt.state.condtitle === "VISIBLE CONDITION")? 
								<label>{item.value}</label>
								:
								<label>{item.const || item.value.value }</label>
							}
							 <button className="btn btn-link" onClick = {() => this.props.cntxt.delCond(item.operation.value, item.col.value, item.value)}>delete</button>
							 </li>
							
						))}
					</ul>	
				</div>
				{(!this.props.cntxt.state.act_c)? <button onClick = {() => this.props.cntxt.setState({modal2:false},() => this.props.cntxt.onSettingsChange(this.props.cntxt.state.cond_title_title, this.props.cntxt.state.cond_col_title,
						this.props.cntxt.state.cond_prop,this.props.cntxt.state.condvalue)
						
					)}
					className = "btn btn-success">OK</button>
					:
					<button onClick = {()=>this.props.cntxt.setState({act_visible_condition:this.props.cntxt.state.condvalue, modal2:false})}className = "btn btn-success">OK</button>
				}
			</Modal>
		);
	}
}
