import React, { Component } from 'react';
import PreLoader, {apishka} from '../Api';
import Config from './Config';
import Select from 'react-select';
import 'react-notifications/lib/notifications.css';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import Filters from './Filters';
import ModalJoin from './JoinModal';
import DefaultModal from './DefaultModal';
import ConditionModal from './ConditionModal';
import Acts from './Acts';
import {Link} from 'react-router-dom';

export default class ViewList extends Component {
	constructor(props){
		super(props);
		this.onTableSel = this.onTableSel.bind(this);
		this.onSettingsChange = this.onSettingsChange.bind(this);
		this.delConfigRow = this.delConfigRow.bind(this);
		this.getTableColums = this.getTableColums.bind(this);
		this.addFilter = this.addFilter.bind(this);
		this.removeFilter = this.removeFilter.bind(this);
		this.saveView = this.saveView.bind(this);
		this.upsAndDown = this.upsAndDown.bind(this);
		this.upAndDownFilter = this.upAndDownFilter.bind(this);
		this.refreshConfig = this.refreshConfig.bind(this);
		this.showSquery = this.showSquery.bind(this);
		this.addRel = this.addRel.bind(this);

		this.setFilterType = this.setFilterType.bind(this);
		this.editFilter = this.editFilter.bind(this);
		this.closeModal = this.closeModal.bind(this);
		this.closeDefModal = this.closeDefModal.bind(this);
		this.getActTypes = this.getActTypes.bind(this);
	
		this.getParamsColumns = this.getParamsColumns.bind(this);
		this.addAct = this.addAct.bind(this);
		this.editAct = this.editAct.bind(this);
		/*this.setActParamCol = this.setActParamCol.bind(this);
		this.setActParamTitle = this.setActParamTitle.bind(this);*/
		this.addActParams = this.addActParams.bind(this);
		this.removeActParam = this.removeActParam.bind(this);
		this.removeAct = this.removeAct.bind(this);
		this.getFunctions = this.getFunctions.bind(this);
		this.newFn = this.newFn.bind(this);
		this.closeModal2 = this.closeModal2.bind(this);
		this.setCondValue = this.setCondValue.bind(this);
		this.delCond = this.delCond.bind(this);
		this.getActApiMethods = this.getActApiMethods.bind(this);
		this.getParamsTypes = this.getParamsTypes.bind(this);
		this.upAndDownAct = this.upAndDownAct.bind(this); 
		
		this.state = {
			background:"#afdaff",
			cardb:"#a8d7ff",
			data:{},
			roles:[],
			functions:[],
			viewroles:[],
			filtertypes:[],
			tables:[],
			viewtypes:[],
			loading:true,
			title:"",
			path:"",
			classname:"",
			desc:"",
			pagination:false,
			viewtype:"",
			table:{},
			tablecolumns:[],
			config:[],
			multiselect:[],
			relationSelectedTable:"",
			orderby:null,
			subscrible:null,
			checker:false,
			groupby:[],
			fncolumns:[],
			fn:"",
			fncol:"",
			/*filters*/
			filters:[],
			filtertitle:"",
			filtercolumn:{},
			filtertype:{},
			flterroles:[],
			filterclassname:"",
			/*filters*/
			
			/*modal*/
			columntypes:[],
			showquery:"Show sql",
			modal:false,
			modal2:false,
			modaltable:null,
			modalconfig:[],
			modalcol:null,
			tjoin: null,
			tpath:[],
			condtitle:"",
			cond_column:null,
			condvalue:[],
			cond_value:null,
			cond_oper:null,
			cond_prop:null,
			cond_col_title:null,
			cond_relation:null,
			
			defmodal:false,
			defvalue:[],
			defval:null,
			defact:null,
			defbool:null,
			def_title:null,
			def_col:null,
			def_table:null,
			/*modal*/
			
			/*acts*/
			actitle:"",
			acticon:"fa fa-eye",
			actypes:[],
			actype:null,
			acts:[],
			actconst:"",
			actapimethod:null,
			actapiconfirm:true,
			actapirefresh:true,	
			
			paramtitle:"",
			paramtype:null,
			paramtypes:[],
			paramt:null,
			paramcolumns:[],
			actmethods:[],
			actparamcolumntitle:"",
			actparamcolumn:"",
			actparamtype:null,
			actparams:[],
			actiseverystr:false,
			actroles:[],
			actact:"",
			actclassname:"",
			actapitype:"GET",
			newconfig:[]
			/*acts*/
		}
	}	
	
	componentDidMount(){
		/**/
		apishka('get', {}, {}, '/api/formparams', 
			(data)=>{
			this.setState({
				filtertypes:data.filtertypes, 
				tables:data.tables, 
				viewtypes:data.viewtypes, 
				columntypes:data.columntypes,
				loading:false
			},() => {
				if (this.props.match.params.id){
					apishka('get', {}, {id:this.props.match.params.id}, '/api/view', 
						(data) => {
							let roles = data.roles	
							data = data.outjson
		
							this.setState({
								roles:roles,	
								groupby:data.groupby, 
								filters:data.filters, 
								orderby:data.orderby, 
								checker:data.checker, 
								subscrible:data.subscrible, 
								pagesize:data.ispagesize, 
								pagecount:data.pagecount, 
								foundcount:data.foundcount, 
								viewroles:data.roles, 
								title:data.title, 
								path:data.path, 
								desc:data.descr, 
								classname:data.classname, 
								pagination:data.pagination,
								table:{value:data.tablename, label:data.tablename}, 
								config:data.config, 
								acts:data.acts, 
								viewtype:{value:data.viewtype, label:data.viewtype} 
							},() => {
								document.title = this.state.title
								if (data.viewtype.indexOf('form') > -1)
									this.setState({background:"#f3b0b0", cardb:"#f3d0d0b5"})	
								apishka('get', {}, {tabname:this.state.table.value}, '/api/tablecolums', 
									(data) => {this.setState({tablecolumns:data.outjson, loading:false})}, 
									(err) => {this.setState({loading:false},() => {
										if (err.response.data) {
											NotificationManager.error(err.response.data.message)
										}
										else {
											NotificationManager.error('unknown error!')
										}
											
									})})
								})
							}, 
						(err) => {this.setState({loading:false},() => {
							if (err.response.data) {
								NotificationManager.error(err.response.data.message)
							} else {
								NotificationManager.error('unknown error!')
							}
						})})
					}
				})
			}, 
			(err)=>{this.setState({loading:false},() => {
				if (err.response.data) {
					NotificationManager.error(err.response.data.message)
				} else {
					NotificationManager.error('unknown error!')
				}							
			})}
		)
	}
	
	onTableSel(selectedOption) {
		/*
			on table select get it columns and config
		*/
		this.setState({loading:true,table:selectedOption},()=>{
			apishka('get', {}, {tabname:selectedOption.value}, '/api/createconfig', 
				(data)=>{this.setState({config:data.outjson},()=>{
					apishka('get', {}, {tabname:selectedOption.value}, '/api/tablecolums', 
						(data)=>{this.setState({tablecolumns:data.outjson, loading:false})}, 
						(err)=>{this.setState({loading:false},() => {
							if (err.response.data) {
								NotificationManager.error(err.response.data.message)
							} else {
								NotificationManager.error('unknown error!')
							}
												
						})}
					)
				})}, 
				(err)=>{this.setState({loading:false},() => {
					if (err.response.data) {
						NotificationManager.error(err.response.data.message)
					} else {
						NotificationManager.error('unknown error!')
					}
												
				})}
			)
		})
	}
	
	delConfigRow(colname, title) {
		if (window.confirm('Confirm delete!')) {
			let config = this.state.config
			config = config.filter((i) => i.col !== colname || i.title !== title)
			this.setState({config:config})
		}
	}
	
	upAndDownAct(ordertype,title, type) {
		/*
			change column position in Acts
		*/
		let acts = this.state.acts
		let index = acts.findIndex(item => item.title === title && item.type === type)
		if (ordertype === 1) {
			if (index>0) {
				let prevobj = acts[index-1]
				acts[index-1] = acts[index]
				acts[index] = prevobj
			}
		} else {
			if (index+1<acts.length) {
				let nextobj = acts[index+1]
				acts[index+1] = acts[index]
				acts[index] = nextobj
			}
		}
		this.setState({acts:acts})
	}
	
	onSettingsChange(title,colname, propname, value,rel = "",t="",dep=false, fromconfig = true, notaddable = false) {
		/*
			change value in config by key 
		*/
		console.log(title)
		let config = this.state.config
		
		if (propname === "relationcolums" ) {
			if (fromconfig === true) {
				config = config.filter((i)=>i.relatecolumn !== colname )
			}
			value.forEach((col) => {
				let confur = config.filter((i) => i.col === col.value && i.t === t )

				if (!dep && !notaddable) {
					
					if (confur.length === 0) {
						apishka(
							'get', {}, {table:rel, col:col.value}, 
							'/api/getcolumnconfig', 
							(data) => {
								data = data.outjson
								let n = false
								if (data.relation) {
									n = true
								}
								col["title"] = col.value
								if (config.filter((obj) => obj.title === col.value).length > 0)
									col['title'] = col.title + "_" 	
								//console.log("T",this.state.tpath)
								config.push({
									"col": col.value, "title": col.title, "type": "text", 
									"visible": 1, "input": 0, "output": 0, "relation": data.relation, 
									"relationcolums": "[]","related":true, "table":rel,"roles":[], 
									"relatecolumn": colname, classname:"",
									"t":t, tpath:this.state.tpath, notaddable:n
								})	
								this.setState({config:config})
								return
							},
							(err) => {
								if (err.response.data) {
									NotificationManager.error(err.response.data.message)
								} else {
									NotificationManager.error('unknown error!')
								}
						})
					}
				}
			})
		}

		let i = 0;
		if (config.filter((x) => x.title === value && x.col !== colname).length > 0 && propname === "title")  {
			value = value + "_1"
		}
		for (i in config) {
			if (config[i].col === colname  && config[i].title === title) {
				config[i][propname] = value;
			}
		}
		this.setState({config:config})
	}
	
	newFn() {
		let config = this.state.config
		if (!this.state.fncol || this.state.fncol === "") {
			NotificationManager.error("enter col title");
			return;
		}
		
		if (!this.state.fn) {
			NotificationManager.error("choose col fn");
			return;
		}
		
		if (!this.state.fncolumns || this.state.fncolumns.length === 0) {
			NotificationManager.error("choose col fncolumns");
			return;
		}
		
		let checker1 = config.filter((obj) => obj.depency && obj.visible)
		if (checker1.length>0 && this.state.fn.functype === 'groupby') {
			NotificationManager.error("You can`t add groupby functions if you have depency columns in visible");
			return;
		}
		
		config.push({
			"col": this.state.fncol, "title": this.state.fncol, "type": "text", "visible": 1, "input": 0, "output": 0, "relation": null, 
			"relationcolums": "[]","related":true, "table":null,"roles":[], "relatecolumn": "", "t":null, tpath:null,
			"fn":this.state.fn, "fncolumns":this.state.fncolumns
		})
		this.setState({config:config})
	}
	
	getTableColums(tabname) {
		/*Table columns*/
		if (this.state.relationSelectedTable !== tabname) {
			this.setState({loading:true, relationSelectedTable:tabname},()=>
				apishka('get', {}, {tabname:tabname}, '/api/tablecolums', 
					(data)=>{this.setState({multiselect:data.outjson, loading:false})}, 
					(err)=>{this.setState({loading:false},() => {
						if (err.response.data) {
							NotificationManager.error(err.response.data.message)
						} else {
							NotificationManager.error('unknown error!')
						}						
				})})
			)		
		}
	}
	
	addFilter() {
		/*
			Add filter in config
		*/
		
		if (!this.state.filtertitle || this.state.filtertitle === "") {
			NotificationManager.error("add filter title");
			return;
		}
		
		if (!this.state.filtercolumn.value && this.state.filtercolumn.length === 0) {
			NotificationManager.error("add filter column");
			return;
		}
		
		if (!this.state.filtertype.value) {
			NotificationManager.error("add filter type");
			return;
		}
		
		const filters = this.state.filters;
		
		if (filters.find(obj=> { return obj.title === this.state.filtertitle })) {
			NotificationManager.error("duplicate title");
			return;
		}
			
		let table = this.state.config.find(obj=> { return obj.col === this.state.filtercolumn.label }) 
		let filtercolumn = this.state.filtercolumn
		//console.log(filtercolumn)
		if (!filtercolumn || filtercolumn === '' || Object.keys(filtercolumn).length === 0){
			NotificationManager.error("add filter column");
			return;
		}
		
		if (this.state.filtertype.value !== 'typehead') {
			table = table.relation
			filtercolumn = this.state.filtercolumn.label
			if (filters.find((x) => x.column === filtercolumn)) {
				NotificationManager.error("can not be two filters with same column");
				return;
			}
		}
		
		filters.push({"title":this.state.filtertitle,"column":filtercolumn, 
			"t": this.state.filtercolumn.t,"type":this.state.filtertype.value, 
			"table":table,
			roles:this.state.flterroles,
			classname:this.state.filterclassname
			})
		this.setState({filters:filters})
	}
	
	editFilter(item) {
		this.removeFilter(item.title)
		let filtercolumn = {value:item.column, label:item.column, t:item.t}
		if (item.type === "typehead")
			filtercolumn = item.column	
		this.setState({
			filtertitle:item.title,
			filtercolumn:filtercolumn, 
			filtertype:{value:item.type, label:item.type}, 
			flterroles:item.roles,
			filterclassname:item.classname
		})
	}	
	
	removeFilter(title) {
		/*
			remove filter 
		*/	
		let filters = this.state.filters;
		
		filters = filters.filter((obj) => { return obj.title !== title; });
		
		this.setState({filters:filters});	
		
	}
	

	saveView() {
		/*
			save view
		*/
		let data = {
			"id":this.props.match.params.id,
			"title":this.state.title,
			"tablename":this.state.table.value,
			"path":this.state.path,
			"descr":this.state.desc,
			"pagination":this.state.pagination,
			"viewtype":this.state.viewtype.value,
			"config":this.state.config,
			"orderby":this.state.orderby,
			"groupby":this.state.groupby,
			"filters":this.state.filters,
			"acts":this.state.acts,
			"classname":this.state.classname,
			"roles":this.state.viewroles,
			"ispagesize":this.state.pagesize,
			"pagecount":this.state.pagecount,
			"foundcount":this.state.foundcount,
			"subscrible":this.state.subscrible,
			"checker":this.state.checker
			
		}
	
		this.setState({loading:true},()=> apishka('post', data, {}, '/api/view', 
			(data) => this.setState({loading:false} ,()=>{
				NotificationManager.success("OK")
				if (!this.props.match.params.id) {
					window.location = '/view/'+data.outjson.id
				}
			}), 
			(err) => this.setState({loading:false},() => {
				if (err.response && err.response.data) {
					NotificationManager.error(err.response.data.message)
				} else {
					NotificationManager.error('unknown error!')
				}
												
			})
		))	
	}
	
	upsAndDown(type, col, title) {
		/*
			change column position in config
		*/
		let config = this.state.config
		let index = config.findIndex(item => item.col === col && item.title === title)
		if (type === 1) {
			if (index>0) {
				let prevobj = config[index-1]
				config[index-1] = config[index]
				config[index] = prevobj
			}
		} else {
			if (index+1<config.length) {
				let nextobj = config[index+1]
				config[index+1] = config[index]
				config[index] = nextobj
			}
		}
		this.setState({config:config})
	}
	
	upAndDownFilter(type, col, title,ftype) {
		/*
			change column position in filters
		*/
		let filters = this.state.filters
		let index = filters.findIndex(item => item.column === col && item.title === title && item.type === ftype)
		if (type === 1) {
			if (index>0) {
				let prevobj = filters[index-1]
				filters[index-1] = filters[index]
				filters[index] = prevobj
			}
		} else {
			if (index+1<filters.length) {
				let nextobj = filters[index+1]
				filters[index+1] = filters[index]
				filters[index] = nextobj
			}
		}
		this.setState({filters:filters})
	}
	
	refreshConfig(){
		/*
			refresh config columns from table
		*/
		this.setState({loading:true},()=>
			apishka('post', {tabname:this.state.table.value, config:this.state.config}, {}, '/api/refreshconfig', 
				(data) => {
					let newconfig = data.outjson
					newconfig.forEach((x) => x.label =  x.col + ' || ' + x.title)
					this.setState({newconfig:newconfig, loading:false},() => NotificationManager.success("OK"))
				}, 
				(err) => this.setState({loading:false},() => {
					if (err.response.data) {
						NotificationManager.error(err.response.data.message)
					} else {
						NotificationManager.error('unknown error!')
					}
												
				})
			)
		)
	}
	
	showSquery(){
		/*
			Show SQL query by config
		*/	
		if (this.state.showquery === "Show sql") {
			this.setState({loading:true, showquery:"Hide sql"},()=> 
				apishka('post', { pagination:{pagenum:1, pagesize:15}, 
						filters:{},
						inputs: {}	
					}, {path:this.state.path}, '/schema/squery', (data) => { this.setState({loading:false,squery:data.squery}) },
				   (err) => this.setState({loading:false},() => {
						if (err.response.data) {
							NotificationManager.error(err.response.data.message)
						} else {
							NotificationManager.error('unknown error!')
						}											
				   }))
			)
		} else {
			this.setState({showquery:"Show sql", squery:""})
		}	
	}
	
	addRel(relationtable, col, tjoin) {
		/*
			Add relation join
		*/	
		if (relationtable && relationtable !== "") {
			this.setState({loading:true},()=>
				apishka('get', {}, {tabname:relationtable}, '/api/createconfig', (data)=>{
						let tpath = this.state.tpath 
						tpath = tpath.filter((x)=>x.table !== relationtable && x.t !== tjoin)	
						tpath.push({'table':relationtable,'t':tjoin,'col':col})
						this.setState({modalconfig:data.outjson,modal:true, modaltable:relationtable, modalcol:col, tjoin:tjoin, loading:false,tpath:tpath})
					},(err)=>this.setState({loading:false},()=>{
						if (err.response.data) {
							NotificationManager.error(err.response.data.message)
						} else {
							NotificationManager.error('unknown error!')
						}							
				   })
				)
			)
		}
	}
	
	setFilterType(e) {
		this.setState({filtertype:e})
	}
	
	closeModal() {
		this.setState({modal:false, modaltable:null})
	}
	
	closeModal2() {
		this.setState({modal2:false, condtitle:"", 		
			cond_column:null,
			condvalue:[],
			cond_value:null,
			cond_oper:null,
			cond_prop:null,
			cond_col_title:null,
			cond_relation:null,
			act_c:false
		})
	}
	
	closeDefModal() {
		this.setState({
			defmodal:false,
			defvalue:[],
			def_title:null,
			def_col:null
		})
	}
		
	getActTypes() {
		if (this.state.actypes.length === 0) {
			this.setState({loading:true},()=>
				apishka('get', {}, {}, '/api/getactypes', (data)=>{
						this.setState({loading:false, actypes:data.outjson})
					},(err)=>this.setState({loading:false},()=>{

						if (err.response.data) {
							NotificationManager.error(err.response.data.message)
						}
						else {
							NotificationManager.error('unknown error!')
						}				
				   })
				)	
			)
		}
	}
	
	getParamsColumns() {
		/**/
		//if (this.state.paramcolumns.length === 0) {
			let config = this.state.config
			
			config = config.filter((item)=>item.type !== 'array')
			let newarr = []
			config.forEach((itm) => {let t = 1; 
				if (itm.related) t = itm.t;
				let col = itm.col
				if (itm.fn) {
					col = itm.fn.value + "(" 
					itm.fncolumns.forEach((x) => col +=  "," + x.label)
					col = col.replace("(,","(") + ")"
				}
				
				return newarr.push({label:col, value:itm.title, t:t}) })
			this.setState({paramcolumns:newarr})	
		//}
	}
	
	addAct() {
		let acts = this.state.acts
		if (!this.state.actitle) {
			NotificationManager.error('no act title!')
			return
		}
		
		if (!this.state.actype) {
			NotificationManager.error('no act type!')
			return
		}		
		
		if (!this.state.actact) {
			NotificationManager.error('no act url!')
			return
		}				
		
		acts.push({
			title:this.state.actitle,
			type:this.state.actype,
			parametrs:this.state.actparams,
			isforevery:this.state.actiseverystr, 
			roles:this.state.actroles,
			act:this.state.actact,
			icon:this.state.acticon,
			classname:this.state.actclassname,
			paramtype:this.state.actparamtype,			
			actapitype:this.state.actapitype,
			act_visible_condition:this.state.act_visible_condition,
			actapimethod:this.state.actapimethod,
			actapiconfirm:this.state.actapiconfirm,
			actapirefresh:this.state.actapirefresh				
		})
		this.setState({acts:acts, actitle:"", actype:null, actparams:[],actroles:[],actparamtype:null})
	}
	
	editAct(item) {
		this.removeAct(item.title)
		this.setState({
			actitle:item.title,
			actype:item.type,
			actparams:item.parametrs,
			actiseverystr:item.isforevery, 
			actroles:item.roles,
			actact :item.act,
			acticon:item.icon,
			actclassname:item.classname,
			actparamtype:item.paramtype,
			actapitype:item.actapitype,
			act_visible_condition:item.act_visible_condition,
			actapimethod:item.actapimethod,
			actapiconfirm:item.actapiconfirm,
			actapirefresh:item.actapirefresh				
		})
	}
		
	addActParams() {
		let actparams = this.state.actparams
		actparams.push({
			paramtitle:this.state.actparamcolumntitle, 
			paramcolumn:this.state.actparamcolumn, 
			paramconst:this.state.actconst,
			paramt:this.state.paramt
		})
		this.setState({actparams:actparams,actparamcolumntitle:"",actparamcolumn:""})
	}
	
	removeActParam(title) {
		/*
			remove act param
		*/	
		let actparams = this.state.actparams;
		
		actparams = actparams.filter((obj) => { return obj.paramtitle !== title; });
		
		this.setState({actparams:actparams});	
	}
	
	removeAct(title) {
		/*
			remove act
		*/	
		let acts = this.state.acts;
		
		acts = acts.filter((obj) => { return obj.title !== title; });
		
		this.setState({acts:acts});	
	}	
	
	getActApiMethods(){
		if (this.state.actmethods.length === 0) 
			this.setState({loading:true},()=>
				apishka('get', {}, {}, '/api/apimethods', (data)=>{
						this.setState({loading:false, actmethods:data.outjson})
					},(err)=>this.setState({loading:false},()=>{
						if (err.response.data) {
							NotificationManager.error(err.response.data.message)
						}
						else {
							NotificationManager.error('unknown error!')
						}				
				   })
				)	
			)
	}
	
	getParamsTypes() {
		if (this.state.paramtypes.length === 0) 
			this.setState({loading:true},()=>
				apishka('get', {}, {}, '/api/paramtypes', (data)=>{
						this.setState({loading:false, paramtypes:data.outjson})
					},(err)=>this.setState({loading:false},()=>{
						if (err.response.data) {
							NotificationManager.error(err.response.data.message)
						}
						else {
							NotificationManager.error('unknown error!')
						}				
				   })
				)	
			)
	}
	
	getFunctions() {
		if (this.state.functions.length === 0) {
			this.setState({loading:true},()=> 
				apishka('get', {}, {}, '/api/getfunctions', (data)=>{
						this.setState({loading:false, functions:data.outjson})
					},(err)=>this.setState({loading:false},()=>{

						if (err.response.data) {
							NotificationManager.error(err.response.data.message)
						} else {
							NotificationManager.error('unknown error!')
						}					
				   })
				)	
			)	
		}
	}
	
	setCondValue() {
		if (!this.state.cond_column) {
			NotificationManager.error("enter condition column")
			return
		}
		
		if (!this.state.cond_value && !this.state.cond_const) {
			NotificationManager.error("enter condition value")
			return
		}	

		if (!this.state.cond_oper) {
			NotificationManager.error("enter condition operation type")
			return
		}	
		let condvalue = this.state.condvalue		
		condvalue.push({col:this.state.cond_column, const:this.state.cond_const, value:this.state.cond_value, operation:this.state.cond_oper})
		this.setState({condvalue:condvalue})
	}
	
	delCond(operation, col, value) {
		let condvalue = this.state.condvalue	
		condvalue = condvalue.filter((obj) => {return !(obj.operation.value === operation && obj.col.value === col 
		&& obj.value === value)} )
		this.setState({condvalue:condvalue})	
	}
	render() {
		return (
			<div 
				style = {{background:this.state.background}}
				 onKeyDown = {(event) => {
					 let charCode = String.fromCharCode(event.which).toLowerCase();
					 if(event.ctrlKey && charCode === 'q') this.saveView()
				 }}
				 className = "col-md-12 " 
			>
				<PreLoader on = {this.state.loading} />
				<ModalJoin
					cntxt = {this}
				/>
				<ConditionModal
					cntxt = {this}
				/>
				<DefaultModal 
					cntxt = {this}
				/>
				<div className = "card" 
					style = {{'background':this.state.cardb}}>
					<p>table:</p>
					{(this.props.match.params.id)?
					<input 
						className = "form-control" 
						value = {this.state.table.value || ""} 
						title = "Major table"
						readOnly />
					: <Select 
						onChange = {(selectedOption)=> this.onTableSel(selectedOption)}
						className = "form-control"
						options = {this.state.tables}
						value = {this.state.table}
						title = "Major table"
					isSearchable 	/>
					}
				</div>	
				<div className = "card" style = {{'background':this.state.cardb}}>
					<p>title:</p>
					<input 
						onChange = {(e)=> this.setState({title:e.target.value})}
						className = "form-control"
						type = "text" 
						title = "view title, will be visible in the page "
						value = {this.state.title} />	
				</div>		
				<div className = "card" style = {{'background':this.state.cardb}}>
					<p>decription:</p>
					<textarea 
						title = "Just a description for developers "
						value = {this.state.desc}
						onChange = {(e)=> this.setState({desc:e.target.value})}
						className = "form-control"></textarea>
				</div>	
				<div className = "card" style = {{'background':this.state.cardb}}>
					<p>path:</p>
					<input
						onChange = {(e)=> this.setState({path:e.target.value})}
						className = "form-control"
						type = "text" 
						title = "Path to this view. The path will take value depending on the type of view. For example: /list/<view path>, /getone/<viewpath>"
						value = {this.state.path} />	
				</div>	
				<div className = "card" style = {{'background':this.state.cardb}}>
					<p>class name:</p>
					<input
						onChange = {(e)=> this.setState({classname:e.target.value})}
						className = "form-control"
						type = "text"
						title = "CSS class name of all component"
						value = {this.state.classname || ""} />		
				</div>	
				<div className = "card" style = {{'background':this.state.cardb}}>				
					<p>view type:</p>
					<Select
						onChange = {(selectedOption)=> this.setState({viewtype:selectedOption},() => {
							if (this.state.viewtype.value.indexOf('form') > -1)
								this.setState({background:"#f3b0b0"})	
							else 
								this.setState({background:"#afdaff"})	
						})}
						value = {this.state.viewtype}
						className = "form-control"
						options = {this.state.viewtypes}
						title = "Type of view. It defines the appearance and behavior of the component."
						isSearchable = {false}
					/>
				</div>	
				<div  className = "card" style = {{'background':this.state.cardb}}>
					{(this.state.viewtype.value && !this.state.viewtype.value.match(/form/))?<div>
					<span>pagination:</span> 
					<input 
						onChange = {(e)=> {
							let p = 0
							if (e.target.checked)
								p = 1	
							this.setState({pagination:p})
						}}
						title = "pagination for list type views"
						className = "form-control"
						type = "checkbox" 
						checked = {this.state.pagination} />
					<span>pagesize:</span> 
					<input 
						onChange = {(e)=> {
							let p = 0
							if (e.target.checked)
								p = 1	
							this.setState({pagesize:p})
						}}
						title = "pagesize for list type views"
						className = "form-control"
						type = "checkbox" 
						checked = {this.state.pagesize} />	
					<span>pagecount:</span> 
					<input 
						onChange = {(e)=> {
							let p = 0
							if (e.target.checked)
								p = 1	
							this.setState({pagecount:p})
						}}
						title = "pagecount for list type views"
						className = "form-control"
						type = "checkbox" 
						checked = {this.state.pagecount} />		
					<span>foundcount:</span> 
					<input 
						onChange = {(e)=> {
							let p = 0
							if (e.target.checked)
								p = 1	
							this.setState({foundcount:p})
						}}
						title = "foundcount for list type views"
						className = "form-control"
						type = "checkbox" 
						checked = {this.state.foundcount} />	
					<span>order by:</span> 
					<input 
						onChange = {(e)=> {
							let p = 0
							if (e.target.checked)
								p = 1	
							this.setState({orderby:p})
						}}
						title = "orderby for list type views"
						className = "form-control"
						type = "checkbox" 
						checked = {this.state.orderby} />		

					<span>checker:</span> 
					<input 
						onChange = {(e)=> {
							let p = 0
							if (e.target.checked)
								p = 1	
							this.setState({checker:p})
						}}
						title = "orderby for list type views"
						className = "form-control"
						type = "checkbox" 
						checked = {this.state.checker} />		
					</div>
					:null}
					<div>
					<span>subscrible:</span> 
					<input 
						onChange = {(e)=> {
							let p = 0
							if (e.target.checked)
								p = 1	
							this.setState({subscrible:p})
						}}
						title = "subscrible, keep ws connection"
						className = "form-control"
						type = "checkbox" 
						checked = {this.state.subscrible} />		
					</div>
				</div>	
				<div className = "card" 
					style = {{'background':this.state.cardb}}>				
					<p>roles:</p>
					<Select
						onChange = {(selectedOption)=> this.setState({viewroles:selectedOption})}
						value = {this.state.viewroles}
						className = "form-control"
						options = {this.state.roles}
						title = "View roles. Available after adding"
						isMulti
					/>
				</div >	
				<div >
					<p>settings:</p>
					<Config 
						cntx = {this}
					/>
				</div>
					{this.state.viewtype.value !== "form full" ?
						<div className = "card" style = {{'background':this.state.cardb}}>
							<Filters 
								cntxt = {this}
							/>
						</div>	
						:
						null
					}
				<hr />
				<div className = "card" style = {{'background':this.state.cardb}}>				
					<Acts 
						cntx = {this}
					/>	
				</div>
				<hr />	
				<div className = "card" style = {{'background':this.state.cardb}}>
					<div>{this.state.squery} 
						<Link className = "btn btn-outline-secondary"  title = 'back' to = '/viewlist'> go back</Link>
						
						<button className = "btn btn-outline-info" 
								title = "show preliminary SQL query"
								onClick = {this.showSquery}>{this.state.showquery}
						</button>
		
					</div>					
					<div>
						<button 
							className = "btn btn-outline-success" 
							onClick = {this.saveView} >Save</button>
					</div>	
				</div>		
				<NotificationContainer/>	
			</div>	
		);
	}
}