import axios from 'axios';
import React, { Component } from 'react';

const api = 'http://94.230.251.78:8080'
export const ws = 'ws://94.230.251.78:8080/ws'

export function apishka(type, data, params, mname, cb, err) {
	
	/*
		Ajax requests
		type - method type (GET,POST,PUT,DELETE)
		data - request body
		params - query parametrs
		mname - method path
		cb - callback when success
		err - callback when error	
	*/

	axios({
		method: type,
		url: api + mname,
		data: data,
		params: params,
		withCredentials: true
	})
	.then(function(response) {
		cb(response.data)
	}, (error) => {
		
		err( error )
			if (error.response && error.response.status === 401 && 
				!(window.location.href.indexOf('/login') === -1 ||
				 window.location.href.indexOf('/home') === -1	
				)) {
			window.location.replace('/home')
		}
		
	});

}

export default class PreLoader extends Component {
	
	render() {
		return (
			<div>
				{(this.props.on)?
				(<div>
					<img 
						alt = "back" 
						onClick = {this.goBack}
						style = {{cursor:"pointer", marginLeft:5, zIndex: 1003, position:"fixed", opacity:0.8}} 
						width = "100%" height = "100%" 
						src = "/pic/preloader.gif"	/>
						
				</div>)
				:
				null
				}
			</div>
		);
	}
	
}

export const Api = api
