import React from 'react';
import ReactDOM from 'react-dom';
import App from './App'
//import {Dashboard} from './components/Dashboard';
import {JDocumentation} from './components/Documentation/JDocumentation';
import {JDocumentation_rus} from './components/Documentation/JDocumentation_rus';

import {FormsDemo} from './components/Sigma/FormsDemo';
import {SampleDemo} from './components/Sigma/SampleDemo';
import {DataDemo} from './components/Sigma/DataDemo';
import {PanelsDemo} from './components/Sigma/PanelsDemo';
import {OverlaysDemo} from './components/Sigma/OverlaysDemo';
import {MenusDemo} from './components/Sigma/MenusDemo';
import {MessagesDemo} from './components/Sigma/MessagesDemo';
import {ChartsDemo} from './components/Sigma/ChartsDemo';
import {MiscDemo} from './components/Sigma/MiscDemo';
import {EmptyPage} from './components/Sigma/EmptyPage';
import {Documentation} from "./components/Sigma/Documentation";
import {SigmaSource} from './components/Sigma/SigmaSource';

//import {Message} from 'primereact/message';
//import {Messages} from 'primereact/messages';
import ViewList from './components/Views/ViewList'
import View from './components/Views/View'
import List from './components/List'
import Tiles from './components/Tiles'
import GetOne from './components/GetOne'
import Compo from './components/Compo'
import Composition from './components/Composition'
import Settings from './components/Settings'
import Auth from './components/Auth'
import LogOut from './components/LogOut'
//import ScrollToTop from './ScrollToTop';
import Userorgs from './components/Userorgs';
import Trees from './components/Trees';

//import classNames from 'classnames';
 import {
    BrowserRouter ,
    Route} from 'react-router-dom';
import './layout/layout.css';
//import './index.css';

const supportsHistory = 'pushState' in window.history

ReactDOM.render(
	
		<BrowserRouter forceRefresh={!supportsHistory}>
			<div >
				<Route path="/" component={App} />
                <Route path="/forms" component={FormsDemo} />
                <Route path="/sample" component={SampleDemo} />
                <Route path="/data" component={DataDemo} />
                <Route path="/panels" component={PanelsDemo} />
                <Route path="/overlays" component={OverlaysDemo} />
                <Route path="/menus" component={MenusDemo} />
                <Route path="/messages" component={MessagesDemo} />
                <Route path="/charts" component={ChartsDemo} />
                <Route path="/sigmasource" component={SigmaSource} />
                <Route path="/misc" component={MiscDemo} />
                <Route path="/empty" component={EmptyPage} />
                <Route path="/documentation" component={Documentation} />
				<Route path="/viewlist" component={ViewList} />
				<Route path="/newview" component={View} />
				<Route path="/view/:id" component={View} />
				<Route path="/list/:path" component={List} />
				<Route path="/getone/:path" component={GetOne} />
				<Route path="/compo/:path" component={Compo} />
				<Route path="/composition/:path" component={Composition} />
				<Route path="/jdocumentation" component={JDocumentation} />
				<Route path="/jdocumentation_rus" component={JDocumentation_rus} />
				<Route path="/projectsettings" component={Settings} />
				<Route path="/login" component={Auth} />
				<Route path="/logout" component={LogOut} />	
				<Route path="/getoneprint/:path" component={GetOne} />
				<Route path="/userorgs" component={Userorgs} />
				<Route path="/trees/:path" component={Trees} />
				<Route path="/tiles/:path" component={Tiles} />
			</div>	
			
		</BrowserRouter  >
,
	document.getElementById('root')
)