import React, { Component } from 'react';
import PreLoader, {apishka} from './Api';
import {NotificationContainer, NotificationManager} from 'react-notifications';

class Auth extends Component {
	
	constructor(props){
		super(props);
		this.logIN = this.logIN.bind(this)
		this.state = {
			login:null,
			pass:null,
			loading:false
		}
		document.title = 'auth'
	}	
	
	logIN() {
		apishka('post', {login:this.state.login, pass:this.state.pass}, {}, '/auth/auth_f', 
			() => {
				window.location = '/'
			},
			(err) => {
				if (err.response && err.response.data) {
					NotificationManager.error(err.response.data.message)
				} else {
					NotificationManager.error('unknown error!')
				}								
			}
		)
	}

	render() {
		return (
			<div className = {this.state.classname || "col-md-10"} onKeyPress={(e) => {
						if (e.key === "Enter") 
							this.logIN()
				}}>
				<PreLoader on = {this.state.loading} />
				<b><label>Login</label></b>
				<input value = {this.state.login || ''} onChange = {(e) => this.setState({login:e.target.value})} className = "form-control" />
				<b><label>Password</label></b>
				<input value = {this.state.pass || ''} onChange = {(e) => this.setState({pass:e.target.value})} type = "password" className = "form-control" />
				<button onClick = {this.logIN}  >Login</button>
				<NotificationContainer/>		
			</div>	
		);
	}
}

export default Auth
