import React, { Component } from 'react';
import PreLoader, {apishka} from './Api'
import Select from 'react-select';
import {NotificationContainer, NotificationManager} from 'react-notifications';
const qs = require('query-string')

class Compo extends Component {
	
	constructor(props){
		super(props);
		this.addRow = this.addRow.bind(this)
		this.addCol = this.addCol.bind(this)
		this.delCol = this.delCol.bind(this)
		this.setViewPath = this.setViewPath.bind(this)
		this.saveCompo = this.saveCompo.bind(this)
		this.state = {
			data:{},
			views:[],
			config:[{
				"rownum":1,
				"cols":[{
					"rownum":1,
					"path":null,
					"heigh":null,
					"width":null
				}]
			}],
			loading:false,
			path:"",
			title:""
		}
	}	
	
	componentDidMount(){
		this.setState({loading:true},()=> apishka('get', {}, {id:qs.parse(this.props.location.search).id}, '/api/compo', 
			(data) => {
				if (data && data.outjson && Object.keys(data.outjson).length>0) {
					
					this.setState({
						loading:false, data:data.outjson, 
						config:data.outjson.config, 
						path:data.outjson.path, 
						title: data.outjson.title
					}) 
				}
			}, 
			(err) => this.setState({loading:false},() => {
				if (err.response && err.response.data) {
					NotificationManager.error(err.response.data.message)
				} else {
					NotificationManager.error('unknown error!')
				}
												
			})
		))	
		this.setState({loading:true},() =>		
			apishka('get', {}, {}, '/api/views', 
				(data)=>{data = data.outjson; data.forEach((x)=>{x.label=x.title; x.value = x.title}); this.setState({views:data, loading:false})}, 
				(err)=>{this.setState({loading:false},() => 
					this.setState({loading:false},()=> {
							if (err.response && err.response.data) {
								NotificationManager.error(err.response.data.message)
							} else {
								NotificationManager.error('unknown error!')
							}	
						}	
					)	
			
			)})	
		)	
	}
	
	addRow() {
		let config = this.state.config
		config.push(
			{
			"rownum":config.length + 1,	
			"cols":[{
					"rownum":1,
					"path":null,
					"heigh":null,
					"width":null
				}]
			}
		)
		this.setState({config:config})
	}
	
	addCol(row) {
		let config = this.state.config
		config.forEach((x) => {
			if (x.rownum === row) {
				x.cols.push({
					"rownum":x.cols.length + 1,
					"path":null,
					"heigh":null,
					"width":null
				})
			}
		})
		this.setState({config:config})
	}
	
	delCol(row,col) {
		let config = this.state.config
		config.forEach((x) => {
			if (x.rownum === row) {
				x.cols = x.cols.filter((obj) => obj.rownum !== col)
			}
		})
		
		this.setState({config:config})
	}
	
	setViewPath(row,col,e) {
		let config = this.state.config
		config[row-1].cols[col-1].path = e
		
		this.setState({config:config})
	}
	
	saveCompo() {
		/*
			save view
		*/
		let data = {
			"id":this.state.data.id,
			"title":this.state.title,
			"path":this.state.path,
			"config":this.state.config
		}
	
		this.setState({loading:true},()=> apishka('post', data, {}, '/api/compo', 
			(data) => {
				let dat = this.state.data
				dat.id = data._id
				this.setState({loading:false, data:dat} ,()=>NotificationManager.success("OK")) 
			}, 
			(err) => this.setState({loading:false},() => {
				if (err.response && err.response.data) {
					NotificationManager.error(err.response.data.message)
				} else {
					NotificationManager.error('unknown error!')
				}
												
			})
		))	
	}
	
	render() {
		return (
			<div>
				<PreLoader on = {this.state.loading} />
				<p><b>compo path</b></p>
				<input							 
					value = {this.state.path} 
					onChange = {(e) => this.setState({path:e.target.value})}
					className = "form-control"
					placeholder = "compo path" />
				<p><b>compo title</b></p>
				<input							 
					value = {this.state.title} 
					onChange = {(e) => this.setState({title:e.target.value})}
					className = "form-control"
					placeholder = "compo title" />	
				<hr />	
				<button
					title = "Add row"
					className = "btn btn-outline-success" 
					onClick = {this.addRow}>
					+
				</button>
				{this.state.config.map((item) => 
					<div className = "row" key = {item.rownum}>
						{item.cols.map((x) => 
							<div className = "col-md" key = {x.path}>
								<Select								
									options = {this.state.views}
									onChange = {(e) => this.setViewPath(item.rownum, x.rownum, e)}	
									value = {x.path}
								/>
								<button 
									title = "Add col"
									className = "btn btn-outline-primary" 
									onClick = {() => this.addCol(item.rownum)}>
									+
								</button>
								<button 
									title = "del col"
									className = "btn btn-outline-danger" 
									onClick = {() => this.delCol(item.rownum, x.rownum)}>
									-
								</button>
							</div>
						)}
					</div>	
				)}
				<button className = "btn btn-outline-success" onClick = {this.saveCompo}>save</button>
				<NotificationContainer/>
			</div>	
		);
	}
}

export default Compo
