import React, { Component } from 'react';
//import {Link} from 'react-router-dom';
import PreLoader, {apishka} from './Api';
//import Select from 'react-select';

import {NotificationContainer, NotificationManager} from 'react-notifications';


class Setting extends Component {
	
	constructor(props){
		super(props);
		this.getData = this.getData.bind(this)
		this.saveData = this.saveData.bind(this)
		this.roleTableSave = this.roleTableSave.bind(this)

		this.state = {
			loading:false,
			dsn:"",
			port:2323,
			developerRole:"0",
			maindomain:"",
			primaryAuthorization:0,
			redirect401:"/auth"
		}
	}	
	
	getData() {
		document.title = 'Project Settings'
		this.setState({loading:true},() => {
			apishka('get', {}, {}, '/admin/getsettings', 
				(data) => {
					this.setState({
						dsn:data.dsn,
						port:data.port,
						developerRole:data.developerRole,
						redirect401:data.redirect401,
						maindomain:data.maindomain,
						primaryAuthorization:data.primaryAuthorization,
						loading:false
					})
				}, 
				(err)=>{this.setState({loading:false},() => {
					if (err.response && err.response.data) {
							NotificationManager.error(err.response.data.message)
						} else {
							NotificationManager.error('unknown error!')
						}
					})
				}
			)
		})
	}
	
	saveData() {
		this.setState({loading:true},() => {
			apishka('post', {settings:this.state}, {}, '/admin/savesettings', 
				(data) => {
					this.setState({
						dsn:data.dsn,
						port:data.port,
						developerRole:data.developerRole,
						maindomain:data.maindomain,
						primaryAuthorization:data.primaryAuthorization,
						loading:false
					},()=>NotificationManager.success("For the changes to take effect - restart the web-server"))
				}, 
				(err)=>{this.setState({loading:false},() => {
					if (err.response && err.response.data) {
							NotificationManager.error(err.response.data.message)
						} else {
							NotificationManager.error('unknown error!')
						}
					})
				}
			)
		})
	}	
	
	roleTableSave(col,val){
		let roletable = this.state.roletable		
		roletable[col] = val
		this.setState({roletable:roletable})
	}
	
	componentDidMount() {
		this.getData()	
	}
	
	render() {
		return (
			<div className = {this.state.classname || "col-md-10"}>
				<PreLoader on = {this.state.loading} />
				<h1>Project settings</h1>
				<b>Database connection string:</b>
				<input className = "form-control" onChange = {(e) => this.setState({dsn:e.target.value})} placeholder = "Database Connection String" value = {this.state.dsn} />
				<b>Server port:</b>
				<input className = "form-control" onChange = {(e) => this.setState({port:e.target.value})} type="number" placeholder = "Database Connection String" value = {this.state.port} />
				<b>Main domain:</b>
				<input className = "form-control" onChange = {(e) => this.setState({maindomain:e.target.value})} type="text" placeholder = "Main domain for static files" value = {this.state.maindomain} />
				<b>Developer role:</b>
				<input className = "form-control" onChange = {(e) => this.setState({developerRole:e.target.value})} type="text" placeholder = "dev role" value = {this.state.developerRole} />
				<b>primary authorization:</b>
				<select
					value = {this.state.primaryAuthorization}
					onChange = {(e) => this.setState({primaryAuthorization:e.target.value})} className = "form-control">
					<option value="1">on</option>
					<option value="0">off</option>
				</select>	
				<hr />	
				<button className="btn btn-success" onClick = {this.saveData}>Save</button>
				<NotificationContainer/>		
			</div>	
		);
	}
}

export default Setting
