import React, { Component } from 'react';
//import {Link} from 'react-router-dom';
import PreLoader, { Api } from './Api';
import Select from 'react-select';
import {NotificationContainer} from 'react-notifications';
import Gallery from 'react-grid-gallery';
import TimePickerWrapper from 'react-times';
import { SketchPicker } from 'react-color';
import 'react-times/css/material/default.css';
import 'react-times/css/classic/default.css';
import * as methods from './Form/FormMethods'
import * as g_methods from './Generic/Methods'
import Acts from './Generic/Acts'
import {AutoComplete} from 'primereact/autocomplete';

const qs = require('query-string')
class GetOne extends Component {
	
	constructor(props){
		super(props);
		this.getData = methods.getData.bind(this);
		this.getSelectData = methods.getSelectData.bind(this);
		this.changeData = methods.changeData.bind(this);
		this.getTypeheadData = methods.getTypeheadData.bind(this);
		this.visibleCondition = g_methods.visibleCondition.bind(this);
		this.getAutocomData = methods.getAutocomData.bind(this);
		this.multiSelectValue = methods.multiSelectValue.bind(this);
		this.getMultiSelectData = methods.getMultiSelectData.bind(this);
		this.getMultiTypeHeadData = methods.getMultiTypeHeadData.bind(this);
		this.callApi = g_methods.callApi.bind(this);
		this.saveFile = methods.saveFile.bind(this);
		this.delFile = methods.delFile.bind(this);
		this.ifParse = g_methods.ifParse.bind(this);
		this.imageSize = methods.imageSize.bind(this);
		this.dateTimeSave = methods.dateTimeSave.bind(this);
		this.sendSocket = g_methods.sendSocket.bind(this);
		this.queryBuilder2 = g_methods.queryBuilder2.bind(this)
		let path = null
		let inputs = null
		
		let nowdate = new Date()
		let search = null
		nowdate =  nowdate.getDate() + '.' + (nowdate.getMonth()+1) + "." + nowdate.getFullYear() + ' '+ nowdate.getHours() + ':' +  nowdate.getMinutes() + ':' + nowdate.getSeconds() 		
		if (this.props.compo) {
			path = this.props.path
			inputs = this.props.inputs
			search = this.props.search
		} else {
			search = this.props.location.search
			inputs = qs.parse(this.props.location.search)
			path = this.props.match.params.path
		}
		
		this.state = {
			inputs:inputs,
			data:{},
			config:[],
			loading:true,
			title:null,
			table:null,
			classname:null,
			path:path,
			acts:[],
			viewid:null,
			subscrible:null,
			socket:false,
			nowdate:nowdate.toString(),
			search:search,
			relation:null,
			relationObj:{}
		}
	}	
	
	componentDidMount(){		
		this.getData();		
	}	
	
	componentWillReceiveProps(n){
		if (n.match)
			if (this.state.path !== n.match.params.path) {
				this.setState({path:n.match.params.path, inputs:qs.parse(n.location.search)},this.getData())
			}
		if (JSON.stringify(n.inputs) !== JSON.stringify(this.state.inputs))
			this.setState({inputs:n.inputs},this.getData())
		
		if (n.compo && n.path !== this.state.path)
			this.setState({path:n.path, search:n.search},this.getData())
	}
	
	render() {
		return (
			<div style = {{margin:"2%"}} className = {this.state.classname || "card"}>
				<PreLoader on = {this.state.loading} />
				<h1>{this.state.title}</h1>
				<div className = "row">
					{this.state.config.filter((item) => item.visible && this.visibleCondition(this.state.data ,item.visible_condition, this.state.inputs)).map((item) => {
						return (
							<div style = {{marginLeft:"1%"}} key = {item.title + item.t} className = {item.classname}>
						
							
							{(() => {switch(item.type) {
								case "select":
								  return (<div key = {item.title}>
											<b>{item.title}</b>
											<Select
												style = {{width:item.width, }}
												
												onFocus = {() => this.getSelectData(item.col)}
												value = {(item.selectdata || []).find(option => option.value === this.state.data[item.title])}
												onChange = {(e) => this.changeData(item.col, item.title, e.value, item)}
												options = {item.selectdata} />
												</div>)
								case "typehead":
								  return (<div key = {item.title}>
											<b>{item.title}</b>
											<div>	
												<AutoComplete 
													minLength={1} 
													placeholder={item.title} 
													
													size={item.width} 
													field="label" 
													suggestions={item.selectdata || []}
													onClick = {(e) => e.target.value = null}
													completeMethod={(e) => {
														let data = this.state.data
														data[item.title] = e.query
														this.setState({data:data})
														if (e.query.length>2) 
															this.getTypeheadData(item, e.query)
														}}
													value = {(item.selectdata || []).find(option => option.value === this.state.data[item.title]) || this.state.data[item.title]}
													onChange={(e) => {if (e.value.value!==undefined) this.changeData(item.col, item.title, e.value.value, item) }} />	
											</div>
										</div>)
								case "autocomplete":
								  return (<div key = {item.title} >
											<b>{item.title}</b>
											<div>	
												<AutoComplete 
													minLength={1} 
													placeholder={item.title} 
													
													size={item.width} 
													field="label" 
													suggestions={item.selectdata}
													completeMethod={(e) => this.getAutocomData(item, e.query)}
													value={this.state.data[item.title]} 
													onChange={(e) => this.changeData(item.col, item.title, e.value.value || e.value, item)} />	
											</div>			
										</div>)				
								case "checkbox":
								  return (<div className = "form-control" key = {item.title}>
											<div><b>{item.title}</b></div>
											<input placeholder = {item.title}
											   style = {{width:item.width}}	
											 
											   checked = {this.state.data[item.title]} 
											   onChange = {(e) => this.changeData(item.col, item.title, e.target.checked, item)} 
											   type = {item.type}
												/>
										</div>)	
								case "multiselect":
								  return (<div key = {item.title + "multi"}>
											<b>{item.title}</b>
											<Select
												isMulti
												style = {{width:item.width}}
												
												onFocus = {() => this.getMultiSelectData(item,'')}
												onKeyDown = {(e) => this.getMultiSelectData(item,e.target.value)}
												value = {this.multiSelectValue(this.state.data[item.title], item.title)}
												onChange = {(e) => this.changeData(item.col, item.title, e, item)}
												options = {item.selectdata} />
												</div>)	
								case "multitypehead":
								  return (<div key = {item.title}>
											<b>{item.title}</b>
											<Select
												isMulti
												style = {{width:item.width}}
												
												onKeyDown = {(e) => {													
														this.getMultiTypeHeadData(item,e.target.value)
													}}	
										
												value = {this.multiSelectValue(this.state.data[item.title], item.title)}
												onChange = {(e) => this.changeData(item.col, item.title, e, item)}
												options = {item.selectdata} />
												</div>)											
								case "label":
								  return (<div  key = {item.title}>
												<b>{item.title}:</b>
												<label

													style = {{width:item.width, marginLeft:5}}
													>{this.state.data[item.title]}</label>
											</div>)	
								case "time":
								  return (<div key = {item.title}>
											<div className="input-group">
												<b><span className="basic-url">{item.title}</span>
												</b>
											</div>
												<TimePickerWrapper
													minuteStep = "1"
													time =  {this.state.data[item.title]}
													onTimeChange = {(e) => {let time = e.hour + ":" + e.minute; this.changeData(item.col, item.title, time, item)}}
												/>
										  </div>)
								case "color":
									return (
									<div key = {item.title}>
										<b><span className="basic-url">{item.title}</span></b>
										<div style = {{width:50, height:50, backgroundColor:this.state.data[item.title]}}>
										</div>
									</div>
								)
								case "colorpicker":
								  return (<div key = {item.title}>
												<b><span className="basic-url">{item.title}</span>
												</b>

												 <SketchPicker
													color={ this.state.data[item.title] }
													onChangeComplete={(e) =>  this.changeData(item.col, item.title, e.hex, item) }
												 />
										  </div>)		  
								case "datetime":
								  return (<div key = {item.title}>
											<div className="input-group">
												<b><span className="basic-url">{item.title}</span>
												</b>
											</div><input type = "date" 
												onBlur = {(e) => this.dateTimeSave(item,e,"date")}
												className = "form-control"
												defaultValue =  {(this.state.data[item.title])?
														(this.state.data[item.title].replace(/\./g,'-').split(' ')[0]).split("-").reverse().join("-")
														:
														null
													} />
												<TimePickerWrapper
													minuteStep = "1"
													time =  {(this.state.data[item.title])? 
														this.state.data[item.title].split(' ')[1]
														:
														null
														}
													onTimeChange = {(e) => this.dateTimeSave(item,e,"time")}
												/>
										  </div>)									  
								case "file":
								  return (	<div key = {item.title}>
												<div className="input-group">
													<b><span className="basic-url">{item.title}</span>
													</b>
												</div>
												<input 
													className = "form-control"
													onBlur = {(e) => this.saveFile(e,item)}
													style = {{width:item.width}} 
													type = "file"
													placeholder = {item.title} />
												<ul>{this.ifParse(this.state.data[item.title]).map((x) => 
													<li key = {x.uri}>{x.label || x.filename}
														<input value = {x.label} onChange = {(e) => this.imageSize(item,x.uri,"label",e.target.value)} placeholder = 'label' />
														<a target="_blank" rel="noopener noreferrer" href = {Api + x.uri}>
															<i className = "fa fa-link"></i>
														</a>
														<button className = "btn btn-light" onClick = {() => this.delFile(item,x.uri)}>
															<i className = "fa fa-times"></i>
														</button>
													</li>)}</ul>
											</div>)	
								case "files":
								  return (	<div key = {item.title}>
												<div><b>{item.title}</b></div>
												<input 
													multiple 
													className = "form-control"
													onBlur = {(e) => this.saveFile(e,item)}
													style = {{width:item.width}} 
													type = "file"
													placeholder = {item.title} />
												<ul>{this.ifParse(this.state.data[item.title]).map((x) => <li key = {x.uri}><div className = "row"><div className = "col-md-3">{x.label || x.filename}</div>  
															<div className = "col-md-1">
																<input value = {x.label} onChange = {(e) => this.imageSize(item,x.uri,"label",e.target.value)} placeholder = 'label' />
																<a target="_blank" rel="noopener noreferrer" href = {Api + x.uri }>
																	<i className = "fa fa-link"></i>
																</a>
																<button className = "btn btn-light" onClick = {() => this.delFile(item,x.uri)}>
																	<i className = "fa fa-times"></i>
																</button>
																
															</div>
														</div>
													</li>)}
												</ul>
											</div>)	
								case "image":
								  return (	<div  key = {item.title}>
												<div className="input-group">
													<b><span className="basic-url">{item.title}</span>
													</b>
												</div>
												
												<input 
													accept="image/*"
													className = {item.classname || "form-control"}
													onBlur = {(e) => this.saveFile(e,item)}
													style = {{width:item.width}} 
													type = "file"
													placeholder = {item.title} />
													<ul>{this.ifParse(this.state.data[item.title]).map((x) => 
														<li key = {x.uri}>{x.filename}
															<input value = {x.height} onChange = {(e) => this.imageSize(item,x.uri,"height",e.target.value)} placeholder = 'height' />
															<input value = {x.width} onChange = {(e) => this.imageSize(item,x.uri,"width",e.target.value)} placeholder = 'width' />
															<a target="_blank" rel="noopener noreferrer" href = {Api + x.uri }>
																<i className = "fa fa-link"></i>
															</a>
															<button className = "btn btn-light" onClick = {() => this.delFile(item,x.uri)}>
																<i className = "fa fa-times"></i>
															</button>
														</li>)}
													</ul>
													
													<Gallery
														enableLightbox={true}
														enableImageSelection={false}
														images={this.ifParse(this.state.data[item.title])} />
													
											</div>)	
								case "images":
								  return (	<div key = {item.title}>
												<div className="input-group">
													<b><span className="basic-url">{item.title}</span>
													</b>
												</div>
												
												<input
													accept="image/*"
													multiple 
													className = {item.classname || "form-control"}
													onBlur = {(e) => this.saveFile(e,item)}
													style = {{width:item.width}} 
													type = "file"
													placeholder = {item.title} />
												
													<ul>{this.ifParse(this.state.data[item.title]).map((x) => 
														<li key = {x.uri}>{x.filename}
															<input value = {x.height} onChange = {(e) => this.imageSize(item,x.uri,"height",e.target.value)} placeholder = 'height' />
															<input value = {x.width} onChange = {(e) => this.imageSize(item,x.uri,"width",e.target.value)} placeholder = 'width' />
															<a target="_blank" rel="noopener noreferrer" href = {Api + x.uri }>
																<i className = "fa fa-link"></i>
															</a>
															<button className = "btn btn-light" onClick = {() => this.delFile(item,x.uri)}>
																<i className = "fa fa-times"></i>
															</button>
														</li>)}
													</ul>
													
													<Gallery
														enableLightbox={true}
														enableImageSelection={false}
														images={this.ifParse(this.state.data[item.title])} />
													
											</div>)		
								case "gallery":
								  return (	<div key = {item.title}>
												<div className="input-group">
													<b><span className="basic-url">{item.title}</span>
													</b>
												</div>
													
													<Gallery
														enableLightbox={true}
														enableImageSelection={false}
														images={this.ifParse(this.state.data[item.title])} />
													
											</div>)		
								case "filelist":
								  return (	<div key = {item.title}>
												<div className="input-group">
													<b><span className="basic-url">{item.title}</span>
													</b>
												</div>
													
												<ul>{this.ifParse(this.state.data[item.title]).map((x) => <li key = {x.uri}><div className = "row"><div className = "col-md-3">{x.label || x.filename}</div>  
															<div className = "col-md-1">
																<a target="_blank" rel="noopener noreferrer" href = {Api + x.uri }>
																	<i className = "fa fa-link"></i>
																</a>
															</div>
														</div>
													</li>)}
												</ul>
											</div>)											
								default:
									return ( 
												<div key = {item.title}>
													<div><b>{item.title}</b></div>
													<input 
														value = {this.state.data[item.title]} 
														onChange = {(e) => {
															let data = this.state.data
															data[item.title] = e.target.value
															this.setState({data:data})
														}}
													className = {item.classname || "form-control"}
													onBlur = {(e) => this.changeData(item.col, item.title, e.target.value, item)} 
													style = {{width:item.width}} type = {item.type} 
													placeholder = {item.title} />
												</div>
													
							)}	
							})()}
						
						
						</div>)
						}
					)}	</div>
					<Acts state = {this.state} cntxt = {this} isforevery = {false} comp = 'form' />
				<NotificationContainer/>		
			</div>	
		);
	}
}

export default GetOne
