import React, { Component } from 'react';
import Select from 'react-select';

export default class Config extends Component {
	render() {
		const tdstyle = {
			borderWidth: '0.03px',
			borderBottomStyle: 'hidden',
			borderLeftStyle: 'solid',
			borderRightStyle: 'solid',
			borderTopStyle: 'solid',
			borderColor: '#807d7f33',
			padding: '2px',
			textAlign: 'center'
		}
		return (
			<div>
				<Select 
					options = {this.props.cntx.state.newconfig}
					placeholder = "ADD COLUMN"
					onChange = {(item) => {
						let config = this.props.cntx.state.config
						let index = config.filter((x) => x.title === item.title ).length
						if (index > 0)
							item.title = item.title + "_" + (index + 1)	
						config.push(item)
						this.props.cntx.setState({config:config})
					}}	
					onFocus = {()=>this.props.cntx.refreshConfig()} />
				<table className = " table-responsive" >
					<thead>
						<tr>
							<th style = {tdstyle}></th>
							<th style = {tdstyle}>column</th>
							<th style = {tdstyle}>title</th>
							<th style = {tdstyle}>type</th>
							<th style = {tdstyle}>roles</th>
							<th style = {tdstyle}>visible</th>
							<th style = {tdstyle}>default value (_userid_, _orgs_ or another const)</th>
							{(this.props.cntx.state.viewtype.label === 'form full' || 
							  this.props.cntx.state.viewtype.label === 'form not mutable')?
							<th style = {tdstyle}>width</th>
							:
							null}
							{(this.props.cntx.state.viewtype.label === 'form full' || 
							  this.props.cntx.state.viewtype.label === 'form not mutable')?
							<th style = {tdstyle}>visible condition</th> : null }

							<th style = {tdstyle}>select condition</th>
							
							<th style = {tdstyle}>join</th>
							{(this.props.cntx.state.viewtype.label === 'form full' || 
							  this.props.cntx.state.viewtype.label === 'form not mutable')?
								<th style = {tdstyle}>updatable</th>
								:
								null
							}
							{(this.props.cntx.state.viewtype.label === 'form full' || 
							  this.props.cntx.state.viewtype.label === 'form not mutable')?
							<th style = {tdstyle}>class name</th> : null
							}
							<th style = {tdstyle}>relation</th>
							<th style = {tdstyle}>relation config</th>
							<th style = {tdstyle}>relation path</th>	
							<th style = {tdstyle}></th>	
						</tr>
					</thead>
					<tbody>							
					{this.props.cntx.state.config.map((item)=>{
						return (<tr key = {item.t + item.col}>
							<td style = {tdstyle}>
								<img alt = "up" 
									 src = "/pic/up.png" 
									 width = "20" 
									 height = "20" 
									 style = {{cursor:"pointer"}}
									 title = "order coumn up"
									 onClick = {()=>this.props.cntx.upsAndDown(1,item.col,item.title)}	/>
								<img 
									alt = "down" 
									src = "/pic/down.png" 
									width = "20" 
									height = "20" 
									style = {{cursor:"pointer"}}
									title = "order coumn down"
									onClick = {()=>this.props.cntx.upsAndDown(2,item.col,item.title)}	/>
							</td>
							<td style = {tdstyle}>
								{(!item.fn)?
										<input title = "table column title like it was declared in table" style = {{width:"100px",backgroundColor:"#cacaca"}} value = {item.col} readOnly/>	
									:
									<input title = "sql function name" style = {{backgroundColor:"#cacaca",width:"100px"}} value = {item.fn.value + "(" + item.fncolumns.map((obj) => obj.label + ", ") + ")"} readOnly/>	
	
								}
								
							</td>
							<td style = {tdstyle}><input title = "Column title in schema. like it should be show in view. (By default - column comment or column title )" style = {{width:"100px"}} size = {31} onChange = {(e)=>{ if (e.target.value.length<=62) this.props.cntx.onSettingsChange(item.title,item.col,"title",e.target.value.trim(), item.table) }} value = {item.title} /></td>
							<td style = {tdstyle}>
								<div style = {{width:"150px"}}>
								<Select 
									
									options = {this.props.cntx.state.columntypes} 
									onChange = {(selectedOption)=>this.props.cntx.onSettingsChange(item.title,item.col,"type",selectedOption.value,item.table)} 
									value = {{label:item.type, value:item.type}} />
								</div>	
							</td>
							<td style = {tdstyle}>
								<div style = {{width:"90px"}}>
									<Select 
										isMulti
										options = {this.props.cntx.state.roles} 
										onChange = {(selectedOption)=>this.props.cntx.onSettingsChange(item.title,item.col,"roles",selectedOption,item.table)} 
										value = {item.roles || []} />
								</div>
							</td>		
							<td style = {tdstyle}><input title = "column visible in view" onChange = {(e)=>this.props.cntx.onSettingsChange(item.title,item.col,"visible",e.target.checked,item.table)} type = "checkbox" defaultChecked = {item.visible} /></td>
							<td style = {tdstyle}><input  title = "value or is null or is not null or func:fn() or constant _orgid,_orgs_,_userid_" 
										style = {{backgroundColor:"#cacaca", width:50}}  
									    onClick = {()=>this.props.cntx.setState({
											defmodal:true,
											def_title:item.title,
											def_col:item.col,
											def_table:item.def_table,
											defvalue:(item.defaultval || [])
										})} 
									    defaultValue = {item.defaultval}
										readOnly />
								        						  
							</td>
							
							{(this.props.cntx.state.viewtype.label === 'form full' || 
							  this.props.cntx.state.viewtype.label === 'form not mutable')?
								<td style = {tdstyle}><input title = "column width style" style = {{width:70}} onChange = {(e)=>this.props.cntx.onSettingsChange(item.title,item.col,"width",e.target.value,item.table)} defaultValue = {item.width} /></td>
								: null
							}
							{(this.props.cntx.state.viewtype.label === 'form full' || 
							  this.props.cntx.state.viewtype.label === 'form not mutable')?
								<td style = {tdstyle}><input 
										title = "Condition of visible. Click it."
										readOnly 
										onClick = {() => this.props.cntx.setState({cond_title_title:item.title, cond_col_title:item.col,cond_prop:'visible_condition',modal2:true, condtitle:"VISIBLE CONDITION", condvalue:(item.visible_condition || [])})}
										style = {{backgroundColor:"#cacaca", width:50}}  
										onChange = {(e)=>this.props.cntx.onSettingsChange(item.title,item.col,"visible_condition",e.target.value,item.table)} 
										defaultValue = {item.visible_condition} />
								</td>
								: null
							}
							
							<td style = {tdstyle}>
								{(item.relation || item.type === "multiselect" || item.type === "multitypehead")? <input 
									readOnly 
									title = "Condition for select and typehead component (WHERE condition in SQL query for that)"
									onClick = {() => this.props.cntx.setState({cond_title_title:item.title,cond_col_title:item.col,cond_prop:'select_condition',modal2:true, condtitle:"SELECT CONDITION",condvalue:(item.select_condition || []), cond_relation:(item.relation || item.multiselecttable) })}
									style = {{backgroundColor:"#cacaca", width:50}}  onChange = {(e)=>this.props.cntx.onSettingsChange(item.title,item.col,"select_condition",e.target.value,item.table)} 
									defaultValue = {item.select_condition} />
								: null
								}
							</td>
							<td style = {tdstyle}><input title = "JOIN relation table. If not checked - LEFT JOIN" onChange = {(e)=>this.props.cntx.onSettingsChange(item.title,item.col,"join",e.target.checked,item.table)} type = "checkbox" defaultChecked = {item.join || false} /></td>
							{(this.props.cntx.state.viewtype.label === 'form full' || 
							  this.props.cntx.state.viewtype.label === 'form not mutable')?
							<td style = {tdstyle}><input title = "After changing this row, form update data" onChange = {(e)=>this.props.cntx.onSettingsChange(item.title,item.col,"updatable",e.target.checked,item.table)} type = "checkbox" defaultChecked = {item.updatable || false} /></td>
							:
							null
							}
							{(this.props.cntx.state.viewtype.label === 'form full' || 
							  this.props.cntx.state.viewtype.label === 'form not mutable')?
								<td style = {tdstyle}><input title = "CSS class name for column" style = {{width:70}} onChange = {(e)=>this.props.cntx.onSettingsChange(item.title,item.col,"classname",e.target.value,item.table)} defaultValue = {item.classname} /></td>
								: null
							}
							<td style = {tdstyle}>
								{(item.relation)? <input 
									title = "FK or depency table. Click it for more deep join"
									style = {{backgroundColor:"#cacaca"}} 
									value = {item.relation || ''}
									onClick = {() => this.props.cntx.addRel(item.relation, item.col, "t" + item.t)}
									readOnly />
									:
									(item.type === "multiselect" || item.type === "multitypehead")?
										<Select
											value = {{label:item.multiselecttable,value:item.multiselecttable}}
											onChange = {(selectedOption)=>this.props.cntx.onSettingsChange(item.title,item.col,"multiselecttable",selectedOption.value,item.table)} 

											options = {this.props.cntx.state.tables}
										/>	
										:
										null
									
								}	
							</td>
							<td style = {tdstyle}>
								{(item.depency)?
									<input
										value = {item.depfunc}
										title = "SQL function for depency table columns/ For example: count - select count(<columns>)"
										onChange = {(e) => this.props.cntx.onSettingsChange(item.title,item.col,"depfunc",e.target.value)}
										placeholder = "function" />
										:
									null
								}
								{(item.relation)?
									<Select options = {this.props.cntx.state.multiselect}
										value = {item.relationcolums}	
										onChange = {(selectedOption)=>this.props.cntx.onSettingsChange(item.title,item.col,"relationcolums",selectedOption,item.relation, item.t, item.depency, false, item.notaddable)}	
										onFocus = {()=>this.props.cntx.getTableColums(item.relation)} isMulti />
									:
										((item.type === "multiselect" || item.type === "multitypehead") && item.multiselecttable)?
										<Select options = {this.props.cntx.state.multiselect}
											value = {item.multicolums}	
											onChange = {(selectedOption)=>this.props.cntx.onSettingsChange(item.title,item.col,"multicolums",selectedOption,item.relation, item.t, item.depency, false, item.notaddable)}	
											onFocus = {()=>this.props.cntx.getTableColums(item.multiselecttable)} isMulti />
										:
										null
										
								}
							</td>
							<td style = {tdstyle}>
								{(item.tpath && item.tpath.length>0)?
									<ul style = {{listStyle:"decimal"}}>
									{item.tpath.map((t)=><li key = {t.table}>{t.table} (<label style = {{color:"#75c6f7"}}>{t.col}</label>)</li>)}
									<li>{item.table} (<label style = {{color:"#75c6f7"}}>{item.relatecolumn}</label>)</li>
									</ul>
								:
									(item.table)?
										<ul style = {{listStyle:"decimal"}}>	
											<li>{item.table} (<label style = {{color:"#75c6f7"}}>{item.relatecolumn}</label>)</li>
										</ul>	
										:
										null
									
								}
							</td>	
							<td style = {tdstyle}>
								<button 
									className = "btn btn-link" 
									title = "delete from schema"
									onClick = {() => this.props.cntx.delConfigRow(item.col, item.title)}>del</button>
							</td>		
						</tr>)
						})
					}	
					</tbody>
				</table>
				<div className = "row">
					<div className = "col-md-2">
						<b>choose function</b>		
					</div >
					<div className = "col-md-2">
						<b>choose columns</b>							
					</div>
					<div className = "col-md-2">
						<b>enter title</b>			
					</div>					
				</div>
				<div className = "row">
					<div className = "col-md-2">
						<Select 
							isSearchable
							options = {this.props.cntx.state.functions}
							value = {this.props.cntx.state.fn}
							onChange = {(selectedOption) => this.props.cntx.setState({fn:selectedOption})}
							onFocus = {() => this.props.cntx.getFunctions()} />			
					</div >
					<div className = "col-md-2">
						<Select 
							isMulti
							options = {this.props.cntx.state.paramcolumns}
							value = {this.props.cntx.state.fncolumns}
							onChange = {(selectedOption) => this.props.cntx.setState({fncolumns:selectedOption})}
							onFocus = {() => this.props.cntx.getParamsColumns()} />						
					</div>
					<div className = "col-md-2">
						<input placeholder = "title" 
							className = "form-control" 
							value = {this.props.cntx.state.fncol} 
							onChange = {(e) => this.props.cntx.setState({fncol:e.target.value})} />					
					</div>					
					<button 
						className = "btn btn-link" 
						title = "Add SQL function in select query"
						onClick = {() => this.props.cntx.newFn()}>add col</button>
				</div>
			</div>
		);
	}
}