import React, { Component } from 'react';
import PreLoader, {apishka} from './Api'
import {NotificationContainer, NotificationManager} from 'react-notifications';
import List from './List'
import Tiles from './Tiles'
import GetOne from './GetOne'
const qs = require('query-string')

class Composition extends Component {
	
	constructor(props){
		super(props);
		this.getData = this.getData.bind(this)
		let path = null
		let inputs = null
		//let nowdate = new Date()
		//let search = null
		//nowdate =  nowdate.getDate() + '.' + (nowdate.getMonth()+1) + "." + nowdate.getFullYear() + ' '+ nowdate.getHours() + ':' +  nowdate.getMinutes() + ':' + nowdate.getSeconds() 
		
		if (this.props.compo) {
			
			path = this.props.path
			inputs = this.props.inputs
			//search = this.props.search
		} else {
			//search = this.props.location.search
			inputs = qs.parse(this.props.location.search)
			path = this.props.match.params.path
		}
		
		
		this.state = {
			data:{},
			config:[],
			loading:false,
			path:path,
			title:"",
			inputs:inputs
		}
	}	
	
	componentDidMount(){
		this.getData()
	}
	
	getData() {
		this.setState({loading:true},()=> apishka('get', {}, {path:this.state.path}, '/api/compobypath', 
			(data) => {
				this.setState({
					loading:false, 
					data:data.outjson, 
					config:data.outjson.config, 
					//path:data.outjson.path, 
					title: data.outjson.title
				},()=>document.title = data.outjson.title) 
				
			}, 
			(err) => this.setState({loading:false},() => {
				if (err.response && err.response.data) {
					NotificationManager.error(err.response.data.message)
				} else {
					NotificationManager.error('unknown error!')
				}
												
			})
		))
	}
	
	componentWillReceiveProps(n){
		if (!n.compo)
			this.setState({inputs:qs.parse(n.location.search)})	

		if (n.match)
			if (this.state.path !== n.match.params.path) 
				this.setState({path:n.match.params.path},this.getData())
			

		/*if (JSON.stringify(n.inputs) !== JSON.stringify(this.state.inputs) )
			this.setState({inputs:n.inputs},this.getData())*/
		if (n.compo && (n.path !== this.state.path || n.search !== this.props.search))
			this.setState({path:n.path, search:n.search, inputs:n.inputs},this.getData())
	}	
		
	render() {
		return (
			<div>
				<PreLoader on = {this.state.loading} />
				{this.state.config.map((item) => 
					<div className = "row">
						{item.cols.map((x) => 
							<div className = {"col-md-" + Math.floor(12 / item.cols.length)}  >
								{(() => {	switch(x.path.viewtype) {
									case "table":
										return (
											<List compo = {true} path = {x.path.path}  inputs = {this.state.inputs} />
										)
									case "tiles":
										return (
											<Tiles compo = {true} path = {x.path.path}  inputs = {this.state.inputs} />
										)	
									case "form full":
										return (
											<GetOne compo = {true} path = {x.path.path} inputs = {this.state.inputs} />
										)
								}})()}	
								
							</div>
						)}
					</div>	
				)}
				<NotificationContainer/>
			</div>	
		);
	}
}

export default Composition
