import React, { Component } from 'react';
import Select from 'react-select';

export default class Filters extends Component {
	render() {
		return (
			<div>
				<h5>filters:</h5>
				<div className = "row">
					<div className = "col-md-3">
						<label>title</label>
						<input
							value = {this.props.cntxt.state.filtertitle}
							onChange = {(e)=>this.props.cntxt.setState({filtertitle:e.target.value})}
							className = "form-control" placeholder = "filter title" />
					</div>
					<div className = "col-md-2">
						<label>column</label>
						<Select
							isMulti = {(this.props.cntxt.state.filtertype.value === "typehead")?
								true
									:
								false
							}
							options = {this.props.cntxt.state.paramcolumns}
							value = {this.props.cntxt.state.filtercolumn}
							onChange = {(selectedOption)=> this.props.cntxt.setState({filtercolumn:selectedOption})}
							onFocus = {() => this.props.cntxt.getParamsColumns()}
						/>
					</div>
					<div className = "col-md-2">
						<label>roles:</label>
						<Select 
							isMulti
							options = {this.props.cntxt.state.roles} 
							onChange = {(selectedOption)=>this.props.cntxt.setState({flterroles:selectedOption})} 
							value = {this.props.cntxt.state.flterroles || []} />
					</div>	
					<div className = "col-md-3">
						<label>filtertype</label>				
						<Select 
							 options = {this.props.cntxt.state.filtertypes}
							 value = {this.props.cntxt.state.filtertype}
							 onChange = {(selectedOption)=> this.props.cntxt.setState({filtertype:selectedOption})}
							/>
						</div>	
					<div className = "col-md-2">
						<label>class name:</label>
						<input
							onChange = {(e)=>this.props.cntxt.setState({filterclassname:e.target.value})}
							className = "form-control" placeholder = "filter class" />
					</div>
				</div>
				<button className="btn btn-outline-primary" onClick = {this.props.cntxt.addFilter}> add filter</button>
				<div id = "filters" >
					<div className = "row">
						<div ></div>
						<div className = "col-md-1"><b>title</b></div>
						<div className = "col-md-3"><b>column</b></div>
						<div className = "col-md-2"><b>type</b></div>
						<div className = "col-md-2"><b>roles</b></div>
						<div className = "col-md-1"><b>class name</b></div>
						<div className = "col-md-1"><b></b></div>
						<div className = "col-md-1"><b></b></div>
					</div>
					{this.props.cntxt.state.filters.map((item)=>{
						return (<div key = {item.title} className = "row">
							<div >
								<img alt = "up" 
									 src = "/pic/up.png" 
									 width = "15" 
									 height = "15" 
									 style = {{cursor:"pointer"}}
									 onClick = {()=>this.props.cntxt.upAndDownFilter(1,item.column,item.title, item.type)}	/>
								<img 
									alt = "down" 
									src = "/pic/down.png" 
									width = "15" 
									height = "15" 
									style = {{cursor:"pointer"}}
									onClick = {()=>this.props.cntxt.upAndDownFilter(2,item.column,item.title, item.type)}	/>
							</div>
							<div className = "col-md-1"><label>{item.title}</label></div>
							<div className = "col-md-3">
								{(item.type === "typehead")?
									(item.column || []).forEach((x) => <label>x.value</label>)
									:
									<label>{item.column}</label>
								}
							</div>
							<div className = "col-md-2"><label>{item.type}</label></div>
							<div className = "col-md-2"><ul>{(item.roles || []).map((itm)=><li key = {itm.value}>{itm.label}</li>)}</ul></div>
							<div className = "col-md-1"><label>{item.classname}</label></div>
							<div className = "col-md-1">
								<button 
									className="btn btn-link" 
									onClick = {() => this.props.cntxt.editFilter(item)}>edit</button>
							</div>
							<div className = "col-md-1">
								<button 
									className="btn btn-link" 
									onClick = {() => this.props.cntxt.removeFilter(item.title)}>delete</button>
							</div>
						</div>)
						})
					}
				</div>	
			</div>	
		);
	}
}