import React, { PureComponent } from 'react';

export default class PageInfo extends PureComponent {
	render () {
		return (
			<div>
				<button className="card-header btn btn-link" 
				   onClick = {() => {
					if (this.props.cntxt.state.pgvis === 'none') 
						this.props.cntxt.setState({pgvis:'block', pgbtitle:'-'})
					else 
						this.props.cntxt.setState({pgvis:'none', pgbtitle:'+'})	
				   }}>
				   page info {this.props.cntxt.state.pgbtitle}
				 </button>
				<div className = "d-print-none card" style = {{display:this.props.cntxt.state.pgvis, background:"#ffffefd9"}}>
					{(this.props.cntxt.state.pagination && this.props.cntxt.state.ispagesize && !this.props.cntxt.state.print)?
						<div className = "d-print-none ">
							<select 
								className = "form-control"
								style = {{width:"15%"}}
								onChange = {(e)=>this.props.cntxt.setState({pagesize:parseInt(e.target.value)},()=>this.props.cntxt.getData())}
								value = {this.props.cntxt.state.pagesize}>
								<option value = "5" >5</option>
								<option value = "10" >10</option>
								<option value = "15" >15</option>
								<option value = "20" >20</option>
								<option value = "25" >25</option>
								<option value = "30" >30</option>
								<option value = "40" >40</option>
								<option value = "50" >50</option>
							</select>			
						</div>
					:
						null
					}
					{(this.props.cntxt.state.ispagecount)? <p className="d-print-none"> pagecount: <b>{this.props.cntxt.state.pagecount}</b></p>: null}
					{(this.props.cntxt.state.isfoundcount)? <p className="d-print-none"> foundcount: <b>{this.props.cntxt.state.foundcount}</b></p>:null}
				</div>
			</div>
		)
	}
}