import React, { PureComponent } from 'react';
import PreLoader /*, {Api}*/ from './Api';
import {NotificationContainer} from 'react-notifications';
import Gallery from 'react-grid-gallery';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import ListFilters from './List/ListFilters'
//import ListPagination from './List/ListPagination'
//import PageInfo from './List/PageInfo'
//import ListActs from './List/ListActs'
//import ListActsForEvery from './List/ListActsForEvery'
import * as methods from './List/ListMethods'
import * as g_methods from './Generic/Methods'
import Acts from './Generic/Acts'
import {Paginator} from 'primereact/paginator';
import {DataTable, Column} from 'primereact/datatable';
//import { TreeTable } from 'primereact/treetable';
const qs = require('query-string')


class List extends PureComponent {
	
	constructor(props){
		super(props);
		this.getData = methods.getData.bind(this);
		this.onFilterChange = methods.onFilterChange.bind(this);
		this.getTable = methods.getTable.bind(this);
		this.setChecked = methods.setChecked.bind(this);
		this.delChecked = methods.delChecked.bind(this);
		this.onFilterChangeDate = methods.onFilterChangeDate.bind(this);
		this.pagination = methods.pagination.bind(this);
		this.delRow = methods.delRow.bind(this)
		this.queryBuilder = g_methods.queryBuilder.bind(this)
		this.queryBuilder2 = g_methods.queryBuilder2.bind(this)
		this.printQueryBuilder = g_methods.printQueryBuilder.bind(this)
		this.ifParse = g_methods.ifParse.bind(this)
		this.format = g_methods.format.bind(this)
		this.orderBy = methods.orderBy.bind(this)
		this.orderByDel = methods.orderByDel.bind(this)
		this.callApi = g_methods.callApi.bind(this)
		this.sendSocket = g_methods.sendSocket.bind(this)
		this.visibleCondition = g_methods.visibleCondition.bind(this);

		let path = null
		let inputs = null
		let nowdate = new Date()
		let search = null
		nowdate =  nowdate.getDate() + '.' + (nowdate.getMonth()+1) + "." + nowdate.getFullYear() + ' ' + nowdate.getHours() + ':' +  nowdate.getMinutes() + ':' + nowdate.getSeconds() 
		
		if (this.props.compo) {
			path = this.props.path
			inputs = this.props.inputs
			
			search = this.props.search
		} else {
			search = this.props.location.search
			inputs = qs.parse(this.props.location.search)
			
			path = this.props.match.params.path
		}
		
		this.state = {
			pagenum:1,
			pagesize:15,
			paramfilters:{},
			inputs:inputs,
			data:[],
			config:[],
			loading:true,
			acts:[],
			filters:[],
			kfilters:{},
			pagination:false,
			pagecount:0,
			foundcount:0,
			table:"",
			path:path,
			isorderby:false,
			checker:false,
			ispagecount:false,
			ispagesize:false,
			isfoundcount:false,
			socket:false,
			nowdate:nowdate.toString(),
			search:search,
			classname:'',
			th:[],
			
			pgvis:'block',
			pgbtitle:'+',
			
			fltvis:'block',
			fltbtitle:'+',
			
			checked:[]
		}
		
	}	
	
	componentDidMount(){
		this.getData('start');
		document.getElementsByTagName("table")[0].setAttribute("id",this.state.path)
	}
	
	componentWillReceiveProps(n){
		if (n.match) {
			if (this.state.path !== n.match.params.path) {
				this.setState({path:n.match.params.path,inputs:qs.parse(n.location.search)}, this.getData(qs.parse(n.location.search)))
			}
		}

		else if (JSON.stringify(n.inputs) !== JSON.stringify(this.state.inputs) ) 
			this.setState({inputs:n.inputs},this.getData('#1'))
			
		
		else if (n.compo && n.path !== this.state.path) 		
			this.setState({path:n.path, search:n.search},this.getData('#2'))

	}
	
	colorTemplate(rowData, item) {
        return <div style={{ width:80,height:50, backgroundColor: item[rowData.title]}}/>;
    }
	setActs(item) {
		return 	<Acts state = {this.state} item = {item}  cntxt = {this} isforevery = {true} comp = 'list' />
	}
	
	Images(itm,item){
		return <Gallery
			enableLightbox={true}
			enableImageSelection={false}
			images={this.ifParse(item[itm.title])} />
	}
	
	Files(itm,item) {
		return <ul>{this.ifParse(item[itm.title]).map((x) => <li key = {x.uri}><div className = "row">
			<div className = "col-md-5">{x.filename}</div>  
				<div className = "col-md-1">
					<a target="_blank" rel="noopener noreferrer" href = { x.uri }>
						<i className = "fa fa-link"></i>
					</a>
				</div>
			</div>
			</li>)}
		</ul>	
	}
	
	inputChecker(item){
		return <input onChange = {(e)=> {											
			if (e.target.checked)
				this.setChecked(item)
			else 
				this.delChecked(item)
			}} type = "checkbox" checked = {this.state.checked.filter((x) => item.rownum === x.rownum)[0]} />
	}
	
	paginator(){
		if (this.state.pagination)
			return <Paginator 
						first={(this.state.pagenum-1)*this.state.pagesize} 
						rows={this.state.pagesize} 
						totalRecords={this.state.foundcount} 
						rowsPerPageOptions={[5,10,20,30,40,50]} 
						leftContent = <div>{(this.state.isfoundcount)? 
							<i className="fa fa-list-ul"> : <b>{this.state.foundcount}</b></i>
								:
							null}</div>
						rightContent = <div>{(this.state.ispagecount)?
								<i className="fa fa-files-o"> : <b>{this.state.pagecount}</b></i>
								: null
							}</div>	
						onPageChange={(e) => this.setState({pagenum: e.page+1, pagesize: e.rows},this.getData())} />
	}
	
	
	TreeT(itm,item) {
		let myitem = item[itm.title]
		return <table>
			<thead>
				<tr>
					{Object.keys(myitem[0]).map((i)=>{return <th key = {i}>{i}</th>})}
				</tr>
			</thead>
			<tbody>
				{myitem.map((it) =>{ return (<tr key = {itm.col + JSON.stringify(it) + itm.title}>
						{Object.keys(it).map((i)=>{return <td key = {i}>{it[i]}</td>})}
					</tr>)}
				)}
			</tbody>													
		</table>
	}
	render() {
		let filteredConfig = this.state.config.filter((x) => x.visible)

		filteredConfig.unshift({col:"rownum",title:"rownum",type:"rownum"})
		filteredConfig.push({col:"...",title:"...",type:"acts"})
		if (this.state.checker) {
			filteredConfig.unshift({col:"",title:"",type:"checker"})
		}
		return (
			<div >
				<PreLoader on = {this.state.loading} />
				<div className = "col-md-10 row d-print-none">
					
					<ReactHTMLTableToExcel
						id="test-table-xls-button"
						className="btn btn-success"
						table={this.state.path}
						filename="tablexlsx"
						sheet="tablexls"
						buttonText=<i className="fa fa-file-excel-o"/>/>
					<Acts state = {this.state} cntxt = {this} isforevery = {false} comp = 'list' />						
				</div>
				<DataTable
					value={this.state.data} 
					reorderableColumns={true}
					resizableColumns={true}
					tableClassName = {this.state.classname || "table"}
					footer = <div>{this.paginator()}</div>
					header = <div><h4>{this.state.title}</h4>
						{(!this.state.print && this.state.filters.length>0)?
						
							<ListFilters cntxt = {this} state = {this.state} />
							: 
								null	
						} 	
						{this.paginator()}
					</div>
					selectionMode="simple"
					onSort = {(item) => {
						console.log(item)
						let conf = filteredConfig.filter((x)=>x.title === item.sortField)[0]
							this.orderBy(
								conf.col , 
								conf.t, 
								conf.related, 
								conf.fn, 
								conf.fncolumns)}} >		
						{filteredConfig.map((item) => {
							if (item.type === "checker")
								return 	<Column 
										style = {{width:50}}
										columnKey = {item.title} 
										body={this.inputChecker.bind(this)} 
										header=<input onChange = {(e)=> {
													let checked = this.state.checked
													let config = this.state.config
													let id = config.filter((x) => x.col === 'id')[0].title
													if (e.target.checked) {
														this.state.data.forEach((item)=> {
															checked = checked.filter((x) => x[id] !== item[id])
															checked.push(item)
														})
													}
													else {
														this.state.data.forEach((item)=>checked = checked.filter((x) => x[id] !== item[id]))
													}
													this.setState({checked:checked})
													}} type = "checkbox" />
												/>		
							else if (item.type === "acts")
								return <Column 
											header = "..."
											columnKey = "acts"
											body = {this.setActs.bind(this)}/>	
							else if (item.type === "rownum")
								return 	<Column 
										style = {{width:50}}
										columnKey = {item.title} 
										field = {item.title} 
										header = "#" 
									/>											
							else if (item.depency)
								return <Column 
										body = {this.TreeT.bind(this,item)}
										header = {item.title} />
							else if (item.type === "color" || item.type === "colorpicker")
								return 	<Column 
										columnKey = {item.title} 
										field = {item.title}
										header = {item.title} 
										sortable = {this.state.isorderby} 
										body = {this.colorTemplate.bind(this,item)}
									/>
							
							else if (item.type === "image" || item.type === "images" || item.types === "picture" || item.type === "gallery")
								return <Column 
											header = {item.title} 
											columnKey = {item.title} 
											body = {this.Images.bind(this,item)}/> 
							else if (item.type === "files" || item.type === "file" || item.type === "filelist")
								return <Column 
											header = {item.title} 
											columnKey = {item.title} 
											body = {this.Files.bind(this,item)}/> 

						    else
								return 	<Column 
										columnKey = {item.title} 
										field = {item.title}
										body = {(itm)=><div>{this.format(itm[item.title])}</div>}
										header = {item.title} 
										sortable = {this.state.isorderby} 
									/>
						})}	
				</DataTable>
				<NotificationContainer/>			
			</div>	
		);
	}
}

export default List
