import React, { PureComponent } from 'react';
import {Link} from 'react-router-dom';
import Modal from 'react-responsive-modal';
import Select from 'react-select';

export default class Acts extends PureComponent {
		
	render() {
		return (
			<div className = "col-md-10 row d-print-none">	
				<Modal open = {this.props.cntxt.state.signmodal} onClose = {() => this.props.cntxt.setState({signmodal:false})} center>
					<div className = "card">
						<h2>Certificates</h2>
						<Select options = {this.props.cntxt.state.certoptions} 
							onChange = {(e) => this.props.cntxt.setState({signcert:e, signmodal:false},() => {
								if (this.props.cntxt.state.signcallback)
									this.props.cntxt.state.signcallback()
							} )} />
					</div>
				</Modal>
				{(this.props.isforevery)?
					<div>
					{this.props.cntxt.state.acts.filter((x) => x.isforevery && this.props.cntxt.visibleCondition(this.props.item,x.act_visible_condition )).map((itm)=>
						{switch (itm.type) {
							case "Link": return (
								<Link 
									className = {itm.classname || "btn"} key = {itm.title} title = {itm.title} 
									style={{ textDecoration: 'none', color:"black" }} 
									to = {itm.act +  this.props.cntxt.queryBuilder(itm, this.props.item) }>
									{(!itm.icon || itm.icon.length===0)?
										<label>{itm.title}</label>
									:
										<i className={itm.icon || itm.classname}></i>
									}												
								</Link>
							)	
							case "API": return (
								<button 
									title = {itm.title} className = {itm.classname || "btn btn"} key = {itm.title} 
									onClick = {() => this.props.cntxt.callApi(itm.act , itm, this.props.item)}>
									{(!itm.icon || itm.icon.length===0)?
										<i>{itm.title}</i>
									:
										<i className={itm.icon || itm.classname}></i>
									}
								</button>
							)
							case "Delete": return (
								<button title = {itm.title} className = {itm.classname || "btn btn"} key = {itm.title} onClick = {() => this.props.cntxt.delRow(this.props.item[this.props.cntxt.state.config.filter((obj)=> obj.col.toLowerCase() === 'id' && !obj.relation)[0].title], itm.roles)}>
									{(!itm.icon || itm.icon.length===0)?
										<i>{itm.title}</i>
									:
										<i className={itm.icon || itm.classname}></i>
									}
								</button>
							)
							default:
								return <div />
						}}
					)}
					</div>
					
					:
					<div>
					{this.props.cntxt.state.acts.filter((x) => !x.isforevery && this.props.cntxt.visibleCondition(this.props.cntxt.state.data,x.act_visible_condition )).map((itm)=>					
						{switch (itm.type) {
							case "Link": return (
								<Link key = {itm.title} title = {itm.title}
									className = {itm.classname || "btn"}
									style={{ textDecoration: 'none', color:"black" }} 
									to = {itm.act + this.props.cntxt.queryBuilder2(itm)} >
									<i className={itm.icon || itm.classname}>
										{ (this.props.comp === 'list')?
											
											(!itm.icon || itm.icon.length===0 )?
												itm.title
											:
												''
											
										:
												" " + itm.title 
												
										}		
									</i>
								</Link>
							)
							case "Delete": return (
								<button className = {itm.classname || "btn"} key = {itm.title} >	
									{(!itm.icon || itm.icon.length===0)?
										<i>{itm.title}</i>
									:
										<i className={itm.icon || itm.classname}></i>
									}
								</button>
							)
							case "API": return (
								<button title = {itm.title} className = {itm.classname || "btn btn"} key = {itm.title} 
									onClick = {() => this.props.cntxt.callApi(itm.act, itm,this.props.cntxt.state.inputs)}>
										<i className={itm.icon || itm.classname}>
										{ (this.props.comp === 'list')?
											
											(!itm.icon || itm.icon.length===0 )?
												itm.title
											:
												''
											
										:
												" " + itm.title  
												
										}		
									</i>
								</button>
							)
							
							default:
								return <div />
						}})
					}	
					</div>
				}				
			</div>
		)
	}
}

