import React, { Component } from 'react';
import Select from 'react-select';

export default class Acts extends Component {
	render() {
		return (
				<div>
					<h5>Acts:</h5>
					<div className = "row">
						<div className = "col-md-2">
							<b>title</b>
							<input
								onChange = {(e)=>this.props.cntx.setState({actitle:e.target.value})}
								value = {this.props.cntx.state.actitle}
								className = "form-control" placeholder = "act title" />
						</div>
						<div className = "col-md-1">
							<b>icon</b>
							<input
								onChange = {(e)=>this.props.cntx.setState({acticon:e.target.value})}
								value = {this.props.cntx.state.acticon}
								className = "form-control" placeholder = "act icon" />
						</div>
						<div className = "col-md-1">
							<b>class</b>
							<input
								onChange = {(e)=>this.props.cntx.setState({actclassname:e.target.value})}
								value = {this.props.cntx.state.actclassname}
								className = "form-control" placeholder = "class name" />
						</div>
						<div className = "col-md-2">
							<b>act</b>				
							<input
								onChange = {(e)=>this.props.cntx.setState({actact:e.target.value})}
								value = {this.props.cntx.state.actact}
								className = "form-control" placeholder = "act" />
						</div>
						<div className = "col-md-1">
							<b>act type</b>				
							<Select 
								 options = {this.props.cntx.state.actypes}
								 value = {{value:this.props.cntx.state.actype,label:this.props.cntx.state.actype}}
								 onChange = {(selectedOption)=> this.props.cntx.setState({actype:selectedOption.value},() => {
									
									if (this.props.cntx.state.actype === "Print Data")
										if ( this.props.cntx.state.viewtype.value === 'table' || this.props.cntx.state.viewtype.value === 'tiles')
											this.props.cntx.setState({actact:'/listprint/'+this.props.cntx.state.path})
										else
											this.props.cntx.setState({actact:'/getoneprint/'+this.props.cntx.state.path})
								 })}
								 onFocus = {this.props.cntx.getActTypes}
								/>
						</div>
						{(this.props.cntx.state.actype === 'API')?
						<div>
							<div>
								<b>API calling method:</b>
								<Select 
									options = {this.props.cntx.state.actmethods}
									onFocus = {this.props.cntx.getActApiMethods}
									value = {{value:this.props.cntx.state.actapimethod,label:this.props.cntx.state.actapimethod}}
									onChange = {(selectedOption)=> this.props.cntx.setState({actapimethod:selectedOption.value})}
								/>
							</div>
							<div>
								<b>API method type:</b>
								<Select 
									options = {[
										{label:'GET',value:'GET'},
										{label:'POST',value:'POST'},
										{label:'DELETE',value:'DELETE'},
										{label:'PUT',value:'PUT'}
									]}
									value = {{value:this.props.cntx.state.actapitype,label:this.props.cntx.state.actapitype}}
									onChange = {(selectedOption)=> this.props.cntx.setState({actapitype:selectedOption.value})}
								/>
							</div>	
							<div>
								<b>refresh data:</b>
								<input
									onChange = {(e)=>this.props.cntx.setState({actapirefresh:e.target.checked})}
									type = "checkbox"
									defaultChecked = {this.props.cntx.state.actapirefresh}
									className = "form-control"  />
							</div>
							<div>
								<b>ask confirm:</b>
								<input
									onChange = {(e)=>this.props.cntx.setState({actapiconfirm:e.target.checked})}
									type = "checkbox"
									checked = {this.props.cntx.state.actapiconfirm }
									className = "form-control"  />
							</div>								
						</div>	
						:
						null	
						}
						
						<div>
							<b>visible condition:</b>
							<div>
								<input 
									title = "Condition of visible. Click it."
									readOnly 
									onClick = {() => this.props.cntx.setState({act_c:true, modal2:true, condtitle:"VISIBLE CONDITION", condvalue:(this.props.cntx.state.act_visible_condition || [])})}
									style = {{backgroundColor:"#cacaca", width:50}}  
									defaultValue = {this.props.cntx.state.act_visible_condition || []} />	
							</div>
								
						</div>
						
						<div className = "col-md-1">
							<b>
								parameters type:
							</b>
							<Select
								value = {{label:this.props.cntx.state.actparamtype,value:this.props.cntx.state.actparamtype}}
								options = {[{label:"query",value:"query"},{label:"link",value:"link"}]}
								onChange = {(e)=>this.props.cntx.setState({actparamtype:e.value})}
							/>	
						</div>	
						

						<div className = "col-md-2">
							<b>roles:</b>
							<Select 
								isMulti
								options = {this.props.cntx.state.roles} 
								onChange = {(selectedOption)=>this.props.cntx.setState({actroles:selectedOption})} 
								value = {this.props.cntx.state.actroles || []} />
						</div>	
						{(this.props.cntx.state.viewtype.value === 'table'
							|| 	this.props.cntx.state.viewtype.value === 'tiles')?
							<div className = "col-md-1">
								<b>for every row</b>				
								<input
									onChange = {(e)=>this.props.cntx.setState({actiseverystr:e.target.checked})}
									type = "checkbox"
									checked = {this.props.cntx.state.actiseverystr}
									className = "form-control"  />
							</div>
						:
							null
						}
						<div className = "col-md-2 card">
							<b>add parametrs (don't forget about: _userid_,_orgid_,_orgs_):</b>	
								
							<input className = "form-control" placeholder = "parametr title" 
									value = {this.props.cntx.state.actparamcolumntitle}
	             				   onChange = {(e)=>this.props.cntx.setState({actparamcolumntitle:e.target.value})} />
							{(this.props.cntx.state.actconst.length === 0)?
									<Select
										value = {this.props.cntx.state.actparamcolumn}
										options = {this.props.cntx.state.paramcolumns}
										onFocus = {this.props.cntx.getParamsColumns}
										onChange = {(e)=>this.props.cntx.setState({actparamcolumn:e})}
									/>
								:
									null
							}	
							
							<input className = "form-control" placeholder = "CONST" 
								value = {this.props.cntx.state.actconst}
								onChange = {(e)=>this.props.cntx.setState({actconst:e.target.value})} />
							{(this.props.cntx.state.actype === 'API')?
									<Select
										value = {{value:this.props.cntx.state.paramt,label:this.props.cntx.state.paramt}}
										options = {this.props.cntx.state.paramtypes}
										onFocus = {this.props.cntx.getParamsTypes}
										onChange = {(e)=>this.props.cntx.setState({paramt:e.value})}
									/>
								:
									null
							}	
							<hr />							
								<b>parametrs:</b>					
								{this.props.cntx.state.actparams.map((item) => <div key = {item.paramtitle}><b>{item.paramtitle} = 
										</b><label>{item.paramconst || item.paramcolumn.value }</label>
											{(item.paramt)?<label>{('  ' + item.paramt) }</label>:null}
									  <button 
										className="btn btn-link" 
										onClick = {() => this.props.cntx.setState({
											actparamcolumntitle:item.paramtitle,
											actconst:item.paramconst,
											paramt:item.paramt,
											actparamcolumn:item.paramcolumn
										},() => this.props.cntx.removeActParam(item.paramtitle))}>edit</button>	
									  <button 
										className="btn btn-link" 
										onClick = {() => this.props.cntx.removeActParam(item.paramtitle)}>delete</button>
										</div>)}
							
							<button onClick = {this.props.cntx.addActParams}>+</button>	
						</div>	
	
						<div className = "col-md-3">
							<button 
								className="btn btn-outline-primary" 
								onClick = {this.props.cntx.addAct}> add act</button>
						</div>

					</div>
					<div style = {{fontSize:"13px"}}>
						<div className = "row" >
							<div className="col-sm-1"></div>
							<div className = "col-sm-1"><b>title</b></div>
							<div className = "col-sm-1"><b>icon</b></div>
							<div className = "col-sm-1"><b>class name</b></div>
							<div className = "col-sm-1"><b>act</b></div>
							<div className = "col-sm-1"><b>type</b></div>
							<div className = "col-sm-1"><b>params type</b></div>
							<div className = "col-sm-1"><b>roles</b></div>
							<div className = "col-sm-1"><b>for every row</b></div>
							<div className = "col-sm-2"><b>parametrs</b></div>
							<div className = "col-sm-1"></div>
						</div>	
						{this.props.cntx.state.acts.map((item) => (<div  className = "row border border-secondary" key = {item.title}>
							<div className = "col-sm-1">
								<img alt = "up" 
									 src = "/pic/up.png" 
									 width = "15" 
									 height = "15" 
									 style = {{cursor:"pointer"}}
									 onClick = {()=>this.props.cntx.upAndDownAct(1,item.title, item.type)}	/>
								<img 
									alt = "down" 
									src = "/pic/down.png" 
									width = "15" 
									height = "15" 
									style = {{cursor:"pointer"}}
									onClick = {()=>this.props.cntx.upAndDownAct(2,item.title, item.type)}	/>
							</div>
							<div className = "col-sm-1"><label>{item.title}</label></div>
							<div className = "col-sm-1"><label>{item.icon}</label></div>
							<div className = "col-sm-1"><label>{item.classname}</label></div>
							<div className = "col-sm-1"><label>{item.act}</label></div>
							<div className = "col-sm-1"><label>{item.type}</label></div>
							<div className = "col-sm-1"><label>{item.paramtype}</label></div>
							<div className = "col-sm-1"><ul>{(item.roles || []).map((itm)=><li key = {itm.value}>{itm.label}</li>)}</ul></div>
							<div className = "col-sm-1"><label>{item.isforevery.toString()}</label></div>
							<div className = "col-sm-2">{(item.parametrs || []).map((itm)=><li key = {itm.paramtitle}>
									<b>{itm.paramtitle}</b> = 
										{itm.paramcolumn? 
											<div>{itm.paramcolumn.value}</div> 
											:
										<label style = {{color:"#8e24ff"}}>{itm.paramconst}</label>}
								</li>)}</div>
							<div className = "col-sm-1">
								<button 
									title = "edit"
									className="btn btn-link" 
									onClick = {() => this.props.cntx.editAct(item)}><i className="fa fa-edit"></i></button>	
								<button 
									title = "delete"
									className="btn btn-link" 
									onClick = {() => this.props.cntx.removeAct(item.title)}><i className="fa fa-trash"></i></button>

							</div>
						</div>)
						)}
					</div>					
				</div>
		);
	}
}

