import React, { PureComponent } from 'react';

export default class ListPagination extends PureComponent {
	render() {
		return (
			<nav >
				{(this.props.cntxt.state.pagination && this.props.cntxt.state.foundcount>this.props.cntxt.state.pagesize)?
					<ul className="pagination d-print-none">
						{(this.props.cntxt.state.pagenum === 1)?
							<li className="page-item disabled">
							  <button className="page-link"  >Previous</button>
							</li>
						:	
							<li className="page-item">
							  <button className="page-link" onClick = {()=>this.props.cntxt.pagination('prev')}>Previous</button>
							</li>					
						}
							
						<input type = "number" value = {this.props.cntxt.state.pagenum} style = {{width:40}} onChange = {()=>{}} />
						{(this.props.cntxt.state.pagenum <= this.props.cntxt.state.pagecount )?
							<li className="page-item">
							  <button className="page-link" onClick = {()=>this.props.cntxt.pagination('next')}>Next</button>
							</li>
						:
							<li className="page-item disabled">
							  <button className="page-link" onClick = {()=>this.props.cntxt.pagination('next')}>Next</button>
							</li>
							}
					 </ul>
				:
					null
				}		  
			</nav>
		)
	}
}