import React, { Component } from 'react';
import classNames from 'classnames';
import {AppMenu} from './AppMenu';

export class AppInlineProfile extends Component {

    constructor(props) {
        super(props);
        this.state = {
            expanded: false,
        };
        this.onClick = this.onClick.bind(this);
    }

    onClick(event) {
        this.setState({expanded: !this.state.expanded});
        event.preventDefault();
    }
	
	componentDidMount() {
		console.log(this.props)
	}

    render() {
        return  (
            <div className="profile">
                <div>
                    <img style = {{borderRadius:"66.3%", width:"90px", height:"90px"}} 
						src={this.props.userdetail.photo || 
						"../assets/layout/images/profile.png"} alt=""	/>
                </div>
                <button className="p-link profile-link" onClick={this.onClick}>
				<span className="username">{this.props.userdetail.fam + ' ' + this.props.userdetail.im}</span>
                    <i className="pi pi-fw pi-cog"/>
                </button>

                <ul className={classNames({'profile-expanded': this.state.expanded})}>
					<AppMenu model={this.props.menu} onMenuItemClick={this.props.onMenuItemClick} />
                </ul>
            </div>
        );
    }
}