import React, { Component } from 'react';
import Modal from 'react-responsive-modal';
import Select from 'react-select';


export default class ModalJoin extends Component {

	render() {
		return (
				<Modal 
					open={this.props.cntxt.state.modal} 
					onClose={this.props.cntxt.closeModal} center>
				  <h2>{this.props.cntxt.state.modaltable}</h2>
				  
				  <div>
				  {this.props.cntxt.state.modalconfig.map((item) =>
					 <div> {(!item.depency)? (<div>	
						<td><input style = {{backgroundColor:"#cacaca"}} value = {item.col} readOnly/></td>
						<td><input value = {item.title} /></td>
						<td><input 
							style = {{backgroundColor:"#cacaca"}} 
							value = {item.relation || ''}
							onClick = {() => this.props.cntxt.addRel(item.relation, item.col, (item.col + item.t + item.column_id + item.type + this.props.cntxt.state.modalconfig.map((e)=>e.col).indexOf(item.col)) )}
							readOnly /></td>
							<td>{(item.relation)?<Select options = {this.props.cntxt.state.multiselect}
													 style = {{width:200}} 	
													 value = {item.relationcolums}	
													 onChange = {(selectedOption)=>this.props.cntxt.onSettingsChange(
																							item.col,
																							item.col,
																							"relationcolums",
																							selectedOption,
																							item.relation, 
																							item.col + item.t + item.column_id + item.type + this.props.cntxt.state.modalconfig.map((e)=>e.col).indexOf(item.col), 
																							item.depency, 
																							this.props.cntxt.state.tjoin, 
																							false)}	
													 onFocus = {()=>this.props.cntxt.getTableColums(item.relation)} isMulti />
							:null}	</td>
					  </div>)
					  :
					  null}	
						 </div>
					) 
				  }
				  </div>
				</Modal>
		);
	}
}
