import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import PreLoader, {apishka} from '../Api'
import {NotificationContainer, NotificationManager} from 'react-notifications';

class ViewList extends Component {
	
	constructor(props){
		super(props);
		this.getData = this.getData.bind(this)
		this.delView = this.delView.bind(this)
		this.showMore = this.showMore.bind(this)
		this.state = {
			data:[],
			loading:true,
			pagesize:30,
			substr:null,
			foundcount:0
		}
	}	
	
	componentDidMount(){
		this.getData()
		document.title = 'View List'
	}
	
	getData() {
		apishka('get', {}, {pagesize:this.state.pagesize, substr:this.state.substr}, '/api/allviews', 
			(data)=>{this.setState({data:data.outjson, foundcount:data.foundcount, loading:false})}, 
			(err)=>{this.setState({loading:false},() => 
				this.setState({loading:false},()=> {
						if (err.response && err.response.data) {
							NotificationManager.error(err.response.data.message)
						} else {
							NotificationManager.error('unknown error!')
						}	
					}	
				)	
		
		)})	
	}

	delView(id) {
		if (window.confirm('Confirm delete!')){ 
			this.setState({loading:true},()=>{
				apishka('delete', {  
					tablename: "framework.views",
					id:id,
					viewid:4037					
				}, {}, '/api/deleterow', (data)=>{
					this.setState({
						loading:false
					},() => this.getData())
					}, (err)=>{
						this.setState({loading:false},() => {
								if (err.response && err.response.data) {
									NotificationManager.error(err.response.data.message)
								}
								else {
									NotificationManager.error('unknown error!')
								}	
						})	
					})	
			})
		}
	}

	showMore() {
		let pagesize = this.state.pagesize + 10
		this.setState({pagesize:pagesize},() => this.getData())
	}
	
	render() {
		return (
			<div>
				<PreLoader on = {this.state.loading} />
				<h5>Views List:</h5>
				<input placeholder = "found" className = "form-control" value = {this.state.substr || ''} onChange = {(e)=>this.setState({substr:e.target.value},()=>this.getData())} />
				<Link to = '/newview' className = "btn btn-outline-success">create view</Link>
				<table className = "table table-sm table-bordered table-hover">
					<thead>
						<tr>
							<th>id</th>
							<th>title</th>
							<th>table</th>
							<th>view type</th>
							<th>description</th>
							<th>go to view</th>
							<th></th>
						</tr>
					</thead>
					<tbody>
					{this.state.data.map((item)=>{
						return (
						<tr key = {item.id}>
							<td>{item.id}</td>
							<td><Link to = {"/view/" + item.id}>{item.title}</Link></td>
							<td>{item.tablename}</td>
							<td>{item.viewtype}</td>
							<td>{item.descr}</td>
							<td><Link to = {item.viewlink + item.path}>{item.viewlink + item.path}</Link></td>
							<td><button 
									className="btn btn-link" 
									onClick = {() => this.delView(item.id)}>del</button>
							</td>
						</tr>)
					})}
					</tbody>
				</table>	
				{(this.state.foundcount>this.state.pagesize)?
					<button onClick = {this.showMore} className = "btn btn-outline-success">SHOW MORE</button> 
					: null}
				<NotificationContainer/>
			</div>	
		);
	}
}

export default ViewList
