import React, { Component } from 'react';
import PreLoader, {apishka} from './Api';
import Select from 'react-select';
import 'react-notifications/lib/notifications.css';
import {NotificationContainer, NotificationManager} from 'react-notifications';


export default class Userorgs extends Component {
	constructor(props){
		super(props);

		this.onOrgSelect = this.onOrgSelect.bind(this)
		this.state = {
			data:[]
		}
	}	
	
	componentDidMount(){
		/**/
		document.title = 'User Orgs'
		apishka('get', {}, {}, '/api/userorgs', 
			(data)=>{
				if (data.outjson.length <= 1 ) {
					window.location = '/getone/myorg?id=_orgid_'
				}
				this.setState({
					data:data.outjson
				})
			}, 
			(err)=>{this.setState({loading:false},() => {
				if (err.response.data) {
					NotificationManager.error(err.response.data.message)
				} else {
					NotificationManager.error('unknown error!')
				}							
			})}
		)
	}
	
	onOrgSelect(val) {
		apishka('post', {orgid:val}, {}, '/api/userorgs', 
			(data)=>{
				window.location = '/getone/myorg?id=_orgid_'
			}, 
			(err)=>{this.setState({loading:false},() => {
				if (err.response.data) {
					NotificationManager.error(err.response.data.message)
				} else {
					NotificationManager.error('unknown error!')
				}							
			})}
		)
	}
	
	render() {
		return (
			<div className = "col-md-12 " style = {{background:"#afdaff"}}>
				<PreLoader on = {this.state.loading} />

				<p>Choose your organization:</p>
				<Select 
					onChange = {(selectedOption)=> this.onOrgSelect(selectedOption.value)}
					className = "form-control"
					options = {this.state.data}
				 	/>
				
				<NotificationContainer/>	
			</div>	
		);
	}
}